package featurea.rml

enum class RmlTagAccess {

    DIRECT, LINK, INDIRECT;

    fun nest(): RmlTagAccess = when (this) {
        DIRECT -> DIRECT
        LINK -> INDIRECT
        INDIRECT -> INDIRECT
    }

}

fun RmlTagAccess.isDirect(): Boolean = this == RmlTagAccess.DIRECT
fun RmlTagAccess.isLink(): Boolean = this == RmlTagAccess.LINK
fun RmlTagAccess.isIndirect(): Boolean = this == RmlTagAccess.INDIRECT
