package featurea.runtime

import kotlin.reflect.KClass

typealias ContainerBlock<T> = (Container) -> T

typealias StaticBlock = Container.() -> Unit

class ContainerNotFoundException(canonicalName: String) : RuntimeException(canonicalName)

class Container(val dependencyRegistry: DependencyRegistry) {

    lateinit var registry: ContainerRegistry
    lateinit var key: String
    lateinit var runtimeBuilder: RuntimeBuilder
    internal var isCreate: Boolean = false

    val modules = ComponentRegistry<Module>()
    val components = ComponentRegistry<Any>()

    fun injectModule(canonicalName: String, module: Module) {
        check(!modules.contains(module))
        check(!modules.containsKey(canonicalName))
        module.key = canonicalName
        modules.inject(module.key, module)
    }

    fun appendModule(canonicalName: String, module: Module) {
        check(!modules.contains(module))
        modules.append(canonicalName, module)
    }

    fun removeModule(module: Module) =
        removeModule(module.key)

    fun removeModule(key: String) {
        modules.remove(key)?.onDestroy()
    }

    fun provideComponent(component: Any) {
        val type = component::class
        val canonicalName = dependencyRegistry.findCanonicalName(type)
        components.inject(canonicalName, component)
        installPlugin(plugin = type)
    }

    inline fun <reified T : Any> findStaticOrNull(): T? {
        val canonicalName = dependencyRegistry.findCanonicalName(T::class)
        return findStaticOrNull(canonicalName)
    }

    fun <T : Any> findStaticOrNull(canonicalName: String): T? {
        val existingComponent = components[canonicalName] as T?
        if (existingComponent != null) {
            return existingComponent
        }

        for (include in runtimeBuilder.containerProvider.includes) {
            val component = include.findStaticOrNull<T>(canonicalName)
            if (component != null) {
                return component
            }
        }

        return null
    }

    inline fun <reified T : Any> import(): T =
        findStaticOrNull() ?: throw DependencyNotFoundException(T::class)

    fun findModule(canonicalName: String): Module =
        findModuleOrNull(canonicalName) ?: throw ModuleNotFoundException(canonicalName)

    fun findModuleOrNull(canonicalName: String): Module? {
        val existingModule = modules[canonicalName]
        if (existingModule != null) {
            return existingModule
        }

        for (includedContainer in runtimeBuilder.containerProvider.includes) {
            val includedModule = includedContainer.findModuleOrNull(canonicalName)
            if (includedModule != null) {
                return includedModule
            }
        }

        return null
    }

    fun destroy() = registry.removeContainer(this)

    /*internals*/

    internal fun onDestroy() {
        runtimeBuilder.containerProvider.builder.onDestroyBlock?.invoke(this)
        for (module in modules) module.destroy()
    }

    private fun installPlugin(plugin: KClass<*>) {
        val features = dependencyRegistry.features[plugin]
        if (features != null) {
            for (feature in features) {
                val containerComponentConstructor = dependencyRegistry.containerComponents[feature]
                if (containerComponentConstructor != null) {
                    val containerComponent = containerComponentConstructor(this)
                    components.inject(feature, containerComponent)
                }
            }
        }
    }

}

@Constructor
fun Container(init: ContainerBuilder.() -> Unit = {}): ContainerBuilder = ContainerBuilder().apply(init)

@Constructor
fun DefaultContainer() = Container()

/*convenience*/

val Container.artifact: Dependency get() = dependencyRegistry.artifact
