package featurea.window

import featurea.Application
import featurea.loader.Loader
import featurea.forEachEvent
import featurea.graphics.Graphics
import featurea.graphics.GraphicsResizeListener
import featurea.layout.Layout
import featurea.layout.Orientation
import featurea.layout.View
import featurea.math.Size
import featurea.replaceWith
import featurea.runtime.*
import kotlin.reflect.KClass

class Window(override val module: Module) : Component {

    private val app = import<Application>()
    private val graphics = import<Graphics>()
    private val layoutService = import<WindowDelegate>()
    private val loader = import<Loader>()

    var layout: Layout = Layout()
    lateinit var orientation: Orientation
    private var oldOrientation: Orientation? = null
    var title: String? by property(::title) { null }
    var size: Size by property(::size)
    val listeners = mutableListOf<WindowListener>()
    private val views: MutableSet<View> = linkedSetOf()
    val widgets = mutableMapOf<View, Widget>()
    internal val widgetProviders = mutableMapOf<KClass<*>, WidgetProvider<View>>()

    init {
        registerPropertyListener(app, app::delegate) {
            resize()
        }
        graphics.listeners.add(GraphicsResizeListener {
            resize()
        })
    }

    fun resizeLayout() {
        val layoutViews = layout.views
        views.removeAll(layoutViews)
        for (view in views) {
            layoutService.removeView(view)
        }
        views.replaceWith(layoutViews)
        for (view in views) {
            layoutService.appendView(view)
        }
        listeners.forEachEvent {
            it.onInvalidateLayout(layout)
        }
    }

    fun changeOrientation(newOrientation: Orientation) {
        if (oldOrientation != newOrientation) {
            oldOrientation = newOrientation
            listeners.forEachEvent {
                it.onChangeOrientation(newOrientation)
            }
        }
    }

    fun resize() {
        if (loader.isLoading) return

        app.delegate?.resize()
        listeners.forEachEvent {
            it.onResize()
        }
    }

    fun invalidate() {
        if (loader.isLoading) return

        app.delegate?.invalidate()
        listeners.forEachEvent {
            it.onInvalidateGraphics()
        }
    }

    fun repeatOnInvalidateGraphics(task: WindowTask) {
        listeners.add(InvalidateGraphicsListener(task))
    }

}

fun DependencyBuilder.WindowPlugin(plugin: Plugin<Window>) = install(plugin)
