package featurea

import featurea.runtime.Module
import featurea.runtime.Component
import featurea.runtime.Constructor
import featurea.runtime.import

abstract class ApplicationController(override val module: Module) : Component {

    private val app = import<Application>()

    var isEnable: Boolean = true

    override fun onCreateComponent() {
        app.controllers.add(this)
    }

    override fun onDestroyComponent() {
        app.controllers.remove(this)
    }

    abstract suspend fun update()

}

@Constructor
fun Component.ApplicationController(update: Block) = object : ApplicationController(module) {
    override suspend fun update() = update()
}
