package featurea.content

import featurea.Bundle
import featurea.System
import featurea.isInstrumentationEnable

class Content {

    val requiredResources = linkedSetOf<String>()
    val existingResources = linkedMapOf<String, Resource>()
    private val readers = linkedMapOf<String, ResourceReader>()
    private val writers = mutableListOf<ResourceWriter>()

    fun registerResourceReader(contentType: String, reader: ResourceReader) {
        readers[contentType] = reader
    }

    fun registerResourceWriter(writer: ResourceWriter) {
        writers.add(writer)
    }

    suspend fun write(rmlTag: ResourceTag, key: String, value: String, bundle: Bundle) {
        var accept: Boolean = true
        for (writer in writers) {
            accept = accept && writer.accept(rmlTag, key, value, bundle)
        }
        if (accept) {
            for (writer in writers) {
                writer.write(rmlTag, key, value, bundle)
            }
        }
    }

    suspend fun flush(bundle: Bundle) {
        for (writer in writers) {
            writer.flush(bundle)
        }
    }

    suspend fun findResource(resourcePath: String, isBundle: Boolean = false): Resource =
        findResourceOrNull(resourcePath, isBundle) ?: throw ResourceNotFoundException(resourcePath)

    suspend fun findResourceOrNull(resourcePath: String, isBundle: Boolean = false): Resource? {
        // 0. instrument resource
        if (isInstrumentationEnable) {
            for ((contentType, reader) in readers) {
                reader.instrumentResource(resourcePath)
            }
        }

        // 1. existing resource
        val existingResource = existingResources[resourcePath]
        if (existingResource != null) {
            return existingResource
        }

        // 2. new resource
        for ((contentType, reader) in readers) {
            val resource = reader.readResourceOrNull(resourcePath, isBundle)
            if (resource != null) {
                resource.contentType = contentType
                resource.path = resourcePath
                existingResources[resourcePath] = resource
                return resource
            }
        }

        // 3. resource not found
        return null
    }

}

/*convenience*/

expect fun System.existsFile(filePath: String): Boolean

expect fun System.findAbsolutePathOrNull(filePath: String): String?
