package featurea.shader

import featurea.content.Resource
import featurea.content.ResourceLoader
import featurea.content.ResourceNotFoundException
import featurea.content.ResourceTag
import featurea.graphics.Graphics
import featurea.graphics.linkShaders
import featurea.log
import featurea.opengl.GLES20
import featurea.opengl.GLProgram
import featurea.runtime.Component
import featurea.runtime.Constructor
import featurea.runtime.Module
import featurea.runtime.import
import featurea.shader.reader.SHADER_CONTENT_TYPE
import featurea.text.TextContent

class ShaderLoader(module: Module) : ResourceLoader(module, SHADER_CONTENT_TYPE) {

    private val gl = import<GLES20>()
    private val graphics = import<Graphics>()
    private val textContent = import<TextContent>()

    internal val shaderMap = mutableMapOf<String, Shader>()

    override fun findResourcesOrNull(resourceTag: ResourceTag, key: String, value: String): List<String>? = null

    override suspend fun loadingResource(resource: Resource) {
        if (shaderMap[resource.path] == null) {
            // 1. read source
            val source: ShaderSource = textContent.readShaderSource(resource.path)

            // 2. create program
            val program: GLProgram = gl.createProgram() ?: error("shader: ${resource.path}")
            graphics.bindAttributes(program, source.attributes)
            graphics.linkShaders(program, source.vs, source.ps)
            for (attribute in source.attributes) {
                val location = gl.getAttribLocation(program, attribute.name)
                if (location == -1) {
                    error("shader: ${resource.path}")
                }
                attribute.location = location
                attribute.offset = source.attributes.offsetOf(location)
            }

            // 3. cache program
            shaderMap[resource.path] = Shader(module, program, source)
        }
        resource.loadingQueue.add(resource.path)
    }

    override suspend fun releaseResource(resource: Resource) {
        // todo
        resource.releaseQueue.add(resource.path)
    }

}

@Constructor
fun Component.provide(shaderPath: String): Shader {
    val shaderProvider = import<ShaderLoader>()
    val shader = shaderProvider.shaderMap[shaderPath] ?: throw ResourceNotFoundException(shaderPath)
    return shader
}

suspend fun TextContent.readShaderSource(shaderPath: String): ShaderSource {
    log("readShaderSource: $shaderPath")
    val text = findTextOrNull(shaderPath) ?: throw ResourceNotFoundException(shaderPath)
    val shaderSource = ShaderSource(text)
    log("readShaderSource: success")
    return shaderSource
}
