package featurea.texture.reader

import featurea.System
import featurea.breakpoint
import featurea.runtime.Container

const val TEXTURES_PACK_DIRECTORY_PATH = ".featurea/cache/textures"
const val TEXTURES_PACK_FILE_NAME = "textures.pack"
const val TEXTURES_PACK_PATH = "$TEXTURES_PACK_DIRECTORY_PATH/$TEXTURES_PACK_FILE_NAME"

class TextureAtlasCache(container: Container) {

    private val system = container.import<System>()
    val textureAtlasMap = linkedMapOf<String, TextureAtlas>()
    val textureRegionMap = linkedMapOf<String, TextureRegion>()

    fun getTextureAtlas(textureRegionPath: String): TextureAtlas {
        val textureRegion = textureRegionMap[textureRegionPath]
        if (system.useTexturePack) {
            if (textureRegion == null) {
                breakpoint()
            }
            return textureRegion!!.atlas
        } else {
            if (textureRegion != null) return textureRegion.atlas
            val textureAtlas = TextureAtlas(textureRegionPath)
            textureAtlasMap[textureRegionPath] = textureAtlas
            return textureAtlas
        }
    }

    fun cacheTextureAtlas(textureAtlas: TextureAtlas) {
        if (system.useTexturePack) {
            // no op
        } else {
            val textureRegion = TextureRegion(textureAtlas, textureAtlas.path,
                    0f, 0f, textureAtlas.size.width, textureAtlas.size.height,
                    0f, 0f, 1f, 1f)
            textureAtlas.textureRegions[textureAtlas.path] = textureRegion
            textureRegionMap[textureRegion.path] = textureRegion
        }
    }

    fun removeTextureAtlas(atlasPath: String) {
        val textureAtlas = textureAtlasMap.remove(atlasPath) ?: return
        textureAtlas.textureRegionPaths.forEach { textureRegionMap.remove(it) }
    }

    fun clear() {
        textureRegionMap.clear()
        textureAtlasMap.clear()
    }

}

/*internals*/

private val textureAtlasPathRegex = "\\.part[0-9]*\\.png".toRegex()

private fun texturePackPathOf(textureAtlasPath: String) = textureAtlasPath.replace(textureAtlasPathRegex, "")
