package featurea.window

import featurea.js.HtmlElementProxy
import featurea.js.setPosition
import featurea.js.setSize
import featurea.layout.View
import featurea.runtime.Module
import featurea.runtime.Component
import featurea.runtime.import
import kotlinx.browser.document
import org.w3c.dom.HTMLElement

actual class Widget(val htmlElement: HTMLElement)

actual class WindowDelegate actual constructor(override val module: Module) : Component {

    private val htmlElement: HTMLElement by lazy { import<HtmlElementProxy>().delegate }
    private val window = import<Window>()

    actual fun appendView(view: View) {
        val widget = findWidget(view) ?: return
        val (x1, y1, x2, y2) = view.rectangle
        val ox = htmlElement.getBoundingClientRect().left.toInt()
        val oy = htmlElement.getBoundingClientRect().top.toInt()
        val x = ox + x1.toInt()
        val y = oy + y1.toInt()
        val width = (x2 - x1).toInt()
        val height = (y2 - y1).toInt()
        widget.htmlElement.setPosition(x, y)
        widget.htmlElement.setSize(width, height)
        widget.htmlElement.style.position = "absolute"
        widget.htmlElement.style.zIndex = "2"
        document.body?.appendChild(widget.htmlElement)
    }

    actual fun removeView(view: View) {
        val widget = window.widgets.remove(view) ?: return
        val viewType = view::class
        val widgetProvider = window.widgetProviders[viewType] ?: return
        with(widgetProvider) {
            destroyWidget(widget)
        }
        widget.htmlElement.style.zIndex = "0"
        widget.htmlElement.style.display = "none"
        document.body?.removeChild(widget.htmlElement)
    }

    /*internals*/

    private fun findWidget(view: View): Widget? {
        val existingWidget = window.widgets[view]
        if (existingWidget != null) return existingWidget
        val viewType = view::class
        val widgetProvider = window.widgetProviders[viewType] ?: return null
        with(widgetProvider) {
            val widget = createWidget(view) ?: return null
            window.widgets[view] = widget
            return widget
        }
    }

}
