package featurea.shader

import featurea.graphics.Graphics
import featurea.graphics.disableProgram
import featurea.graphics.useProgram
import featurea.opengl.GLES20
import featurea.opengl.GLProgram
import featurea.runtime.Module
import featurea.runtime.Component
import featurea.runtime.import

open class Shader(override val module: Module, val id: GLProgram, val source: ShaderSource) : Component {

    val attributes: List<VertexAttribute> get() = source.attributes
    val gl = import<GLES20>()
    val graphics = import<Graphics>()

    @Deprecated("Replace Graphics with Layer.")
    fun use(vertexBuffer: VertexBuffer, block: Shader.() -> Unit) {
        try {
            graphics.useProgram(id)
            vertexBuffer.enable(isInvalid = true)
            block()
        } finally {
            vertexBuffer.disable()
            graphics.disableProgram()
        }
    }

}
