package featurea.runtime

fun injectDependencies(dependencies: Dependency, runtime: Artifact.() -> RuntimeBuilder): RuntimeBuilder {
    val artifact = Artifact(dependencies)
    return artifact.runtime()
}

fun injectStaticDependencies(dependencies: Dependency, staticBlock: StaticBlock) {
    val runtimeBuilder = RuntimeBuilder()
    runtimeBuilder.injectDefaultContainer(dependencies)
    val container: Container = runtimeBuilder.containerProvider.providerContainer(runtimeBuilder)
    container.isCreate = true // quickfix todo improve
    container.staticBlock()
}
