package learnopengl

import featurea.color.Colors.redColor
import featurea.opengl.*
import featurea.runtime.Module
import featurea.shader.ShaderSource

fun LinesTest(module: Module) = object : Test(module) {

    lateinit var arrayBuffer: GLArrayBuffer
    lateinit var shaderProgram: GLProgram
    lateinit var shaderSource: ShaderSource

    override suspend fun load() {
        arrayBuffer = gl.createBuffer() ?: return
        gl.bindArrayBufferData(arrayBuffer, floatArrayOf(-0.5f, 0.5f, 0.5f, 0.5f, 0f, -0.9f), STATIC_DRAW)
        val (shaderProgram, shaderSource) = loadShaderOrNull("shaders/testLines.shader") ?: return
        this.shaderProgram = shaderProgram
        this.shaderSource = shaderSource
    }

    override fun update(elapsedTime: Float) {
        gl.clearColorBuffer(0.2f, 0.3f, 0.3f, 1.0f)
        gl.bindArrayBufferShader(arrayBuffer, shaderProgram, shaderSource)
        gl.setUniform(shaderProgram, "tint", redColor)
        gl.enable(LINE_SMOOTH)
        gl.drawArrays(LINE_LOOP, 0, 3)
    }

}
