package featurea

@Target(AnnotationTarget.TYPE, AnnotationTarget.EXPRESSION)
@Retention(AnnotationRetention.SOURCE)
annotation class Keep

typealias Block = () -> Unit

typealias StringBlock = (String) -> Unit

const val GIF_CACHE_PATH: String = ".featurea/cache/gifs"
const val FONT_CACHE_PATH: String = ".featurea/cache/fonts"
const val SHADER_CACHE_PATH: String = ".featurea/cache/shaders"

var isInstrumentationEnable: Boolean = false

expect val featureaDir: String

expect suspend fun <T> executeAsyncJsAction(action: String, vararg args: String): T

fun breakpoint() {
    log("breakpoint")
}

fun skip() {
    log("skip")
}

fun deprecated(): Nothing = throw UnsupportedOperationException()

fun <T> T?.ifNotNull(block: (receiver: T) -> Unit) {
    val receiver: T? = this
    if (receiver != null) {
        block(receiver)
    }
}
