package featurea.color

import featurea.toHexString

data class Color(val red: Float, val green: Float, val blue: Float, val alpha: Float) {
    constructor(value: Float) : this(value, value, value, value)
}

fun Color.toResource(): String =
    "#${red.toHexString(255)}${green.toHexString(255)}${blue.toHexString(255)}${alpha.toHexString(255)}"

fun String.isValidColorResource(): Boolean = try {
    val rgba = replace("#", "")
    rgba.toLong(16)
    rgba.length == 8
} catch (skip: Throwable) {
    false
}
