package featurea

import featurea.runtime.*

class Application(override val module: Module) : Component {

    val controllers = mutableListOf<ApplicationController>()
    var delegate: ApplicationDelegate? by property(::delegate) { null }
    var elapsedTime: Float = 0f
    var isEnable: Boolean = true
    val listeners = mutableListOf<ApplicationListener>()
    val tasksToRepeatOnUpdateApplication = mutableListOf<(elapsedTime: Float) -> Unit>()
    val tasksToRunOnUpdate = BufferedList<Block>()
    val tasksWithPeriodToRepeatOnUpdate = mutableMapOf<Block, Float>()
    val tasksWithPeriodToRepeatOnUpdateInitial = mutableMapOf<Block, Float>()
    val tasksWithDelayToRunOnUpdate = mutableMapOf<Block, Float>()
    val tasksToRunOnCompleteLoading = BufferedList<ComponentTask>()
    val tasksToRepeatOnCompleteLoadingApplication = mutableListOf<ComponentTask>()
    val tasksToRepeatOnStartLoading = mutableListOf<ComponentTask>()

    fun onCreate() {
        delegate?.create()
        listeners.forEachEvent { it.onCreateApplication() }
    }

    fun onStart() {
        delegate?.start()
        listeners.forEachEvent { it.onStartApplication() }
    }

    fun onResume() {
        delegate?.resume()
        listeners.forEachEvent { it.onResumeApplication() }
    }

    fun onPause() {
        delegate?.pause()
        listeners.forEachEvent { it.onPauseApplication() }
    }

    fun onStop() {
        delegate?.stop()
        listeners.forEachEvent { it.onStopApplication() }
    }

    fun onDestroy() {
        delegate?.destroy()
        listeners.forEachEvent { it.onDestroyApplication() }
    }

    fun repeatOnStartLoading(task: ComponentTask) {
        tasksToRepeatOnStartLoading.add(task)
    }

    fun runOnCompleteLoading(task: ComponentTask) {
        tasksToRunOnCompleteLoading.add(task)
    }

    fun repeatOnCompleteLoadingApplication(task: ComponentTask) {
        tasksToRepeatOnCompleteLoadingApplication.add(task)
    }

    fun runOnUpdate(task: Block) {
        tasksToRunOnUpdate.add(task)
    }

    fun runOnUpdate(delay: Float, task: Block) {
        tasksWithDelayToRunOnUpdate[task] = delay
    }

    fun repeatOnUpdate(task: (elapsedTime: Float) -> Unit) {
        tasksToRepeatOnUpdateApplication.add(task)
    }

    fun repeatOnUpdate(period: Float, task: Block) {
        tasksWithPeriodToRepeatOnUpdate[task] = period
        tasksWithPeriodToRepeatOnUpdateInitial[task] = period
    }

}
