package featurea.rml

import featurea.ApplicationDelegate
import featurea.content.ResourceTag
import featurea.runtime.Module
import featurea.runtime.Component
import featurea.script.Script

open class RmlApplicationBuilder(override val module: Module) : Component, RmlResourceBuilder<Script, Any, ApplicationDelegate> {

    override suspend fun createRoot(rmlResource: RmlResource, rmlTag: ResourceTag, access: RmlTagAccess): Script {
        val rmlSchema = rmlResource.rmlFile.rmlSchema
        val canonicalClassName = rmlSchema.canonicalClassNameByKey[rmlTag.name] ?: error("name: ${rmlTag.name}")
        val script = module.createComponent<Script>(canonicalClassName)
        script.executeAction("create")
        return script
    }

    override suspend fun createOriginal(rmlResource: RmlResource, rmlTag: ResourceTag, access: RmlTagAccess, root: Script): Any {
        if (access.isLink()) return root
        val result = root.executeAction("build")
        return result as Any
    }

    override suspend fun createResult(rmlResource: RmlResource, rmlTag: ResourceTag, original: Any): ApplicationDelegate {
        return original as ApplicationDelegate
    }

    override suspend fun assignAttribute(rmlResource: RmlResource, parent: ResourceTag, parentAccess: RmlTagAccess, parentRoot: Any, key: String, value: String) {
        check(parentRoot is Script)
        parentRoot.executeAction(key, listOf(parentAccess.isLink(), value))
    }

    override suspend fun removeAttribute(rmlResource: RmlResource, parent: ResourceTag, parentAccess: RmlTagAccess, parentRoot: Any, key: String, skip: String) {
        check(parentRoot is Script)
        parentRoot.executeAction(key, listOf(parentAccess.isLink(), null))
    }

    override suspend fun assignProperty(rmlResource: RmlResource, parent: ResourceTag, parentAccess: RmlTagAccess, parentRoot: Any, key: String, property: ResourceTag, propertyOriginal: Any) {
        check(parentRoot is Script)
        parentRoot.executeAction(key, listOf(parentAccess.isLink(), propertyOriginal))
    }

    override suspend fun removeProperty(rmlResource: RmlResource, parent: ResourceTag, parentAccess: RmlTagAccess, parentRoot: Any, key: String, property: ResourceTag, propertyOriginal: Any) {
        throw UnsupportedOperationException("onRemoveProperty")
    }

    override suspend fun appendChild(rmlResource: RmlResource, parent: ResourceTag, parentAccess: RmlTagAccess, parentRoot: Any, child: ResourceTag, childOriginal: Any) {
        check(parentRoot is Script)
        parentRoot.executeAction("append", listOf(parentAccess.isLink(), childOriginal))
    }

    override suspend fun insertChild(rmlResource: RmlResource, parent: ResourceTag, parentAccess: RmlTagAccess, parentRoot: Any, index: Int, child: ResourceTag, childOriginal: Any) {
        check(parentRoot is Script)
        parentRoot.executeAction("insert", listOf(parentAccess.isLink(), index, childOriginal))
    }

    override suspend fun removeChild(rmlResource: RmlResource, parent: ResourceTag, parentAccess: RmlTagAccess, parentRoot: Any, child: ResourceTag, childOriginal: Any) {
        check(parentRoot is Script)
        parentRoot.executeAction("remove", listOf(parentAccess.isLink(), childOriginal))
    }

    override suspend fun replaceChild(rmlResource: RmlResource, parent: ResourceTag, parentAccess: RmlTagAccess, parentRoot: Any, child: ResourceTag, index: Int, childOriginal: Any) {
        check(parentRoot is Script)
        parentRoot.executeAction("replace", listOf(parentAccess.isLink(), index, childOriginal))
    }

}

suspend fun Module.buildApplication(resourcePath: String, source: String? = null): ApplicationDelegate {
    val rmlContent = importComponent<RmlContent>()
    val rmlResource = rmlContent.findRmlResource(resourcePath, source)
    val appDelegate = buildApplication(rmlResource)
    return appDelegate
}

suspend fun Module.buildApplication(rmlResource: RmlResource): ApplicationDelegate {
    val builder = initRmlResourceBuilder(rmlResource, "featurea.app.ApplicationBuilder")
    val resource = with(rmlResource) { rmlTag.createRmlTagEndObject<Any>() }
    val result = builder.createResult(rmlResource, rmlResource.rmlTag, resource)
    val appDelegate = result as ApplicationDelegate
    return appDelegate
}
