package featurea.window

import featurea.Application
import featurea.loader.Loader
import featurea.runtime.Component
import featurea.runtime.Module
import featurea.runtime.import
import featurea.updateTasksWithDelay

class WindowLoop(override val module: Module) : Component {

    private val app = import<Application>()
    private val loader = import<Loader>()

    suspend fun onTick(elapsedTime: Float) {
        app.elapsedTime = if (app.isEnable) elapsedTime else 0f // quickfix todo improve

        app.updateControllers() // suspended because of `ResourceLoader` todo extract logic to its own LoaderLoop
        app.update()
        app.updateTasks()
    }

    /*internals*/

    private suspend fun Application.updateControllers() {
        for (controller in controllers.toList()) { // quickfix todo improve
            controller.update()
        }
    }

    private fun Application.update() {
        val delegate = delegate ?: return

        if (!loader.isLoading) {
            delegate.update(elapsedTime)
            for (repeatTaskOnUpdate in tasksToRepeatOnUpdateApplication) {
                repeatTaskOnUpdate(elapsedTime)
            }
        }
    }

    private fun Application.updateTasks() {
        tasksWithPeriodToRepeatOnUpdate.updateTasksWithDelay(elapsedTime, tasksWithPeriodToRepeatOnUpdateInitial)
        tasksToRunOnUpdate.poll { oldTasks, newTasks ->
            for (oldTask in oldTasks) oldTask()
            for (newTask in newTasks) newTask()
        }
        tasksWithDelayToRunOnUpdate.updateTasksWithDelay(elapsedTime)
    }

}
