package featurea.shader

import featurea.breakpoint
import featurea.graphics.Graphics
import featurea.opengl.*
import featurea.runtime.Constructor
import featurea.runtime.Component
import featurea.runtime.import

class VertexBuffer(internal val gl: GLES20, val attributes: Attributes, var capacity: Int, val stride: Int) {

    private var attributeCount: Int = 0
    val size: Int get() = attributeCount / attributes.vertexSize
    val id: GLArrayBuffer = gl.createBuffer() ?: error("array buffer not created") // todo rename `id` to `selector`
    var data: FloatArray = createData()

    fun vertex(vararg floats: Float) {
        for (float in floats) {
            val index = attributeCount++
            if (index >= data.size) {
                breakpoint()
            }
            require(index < data.size) { "$index >= ${data.size}" }
            data[index] = float
        }
    }

    fun enable(isInvalid: Boolean = true) {
        gl.enableArrayBuffer(id, attributes, data, isInvalid)
    }

    fun disable() {
        gl.disableArrayBuffer(attributes)
    }

    fun clear() {
        attributeCount = 0
    }

    fun destroy() {
        gl.deleteBuffer(id)
    }

    fun ensureCapacity(capacity: Int) {
        if (this.capacity == capacity) return

        this.capacity = capacity
        data = createData()
    }

    fun reset(capacity: Int) {
        clear()
        ensureCapacity(capacity)
    }

    /*internals*/

    private fun createData(): FloatArray {
        return FloatArray(capacity * stride * attributes.vertexSize)
    }

}

fun VertexBuffer.renderTriangles(isInvalid: Boolean = true) {
    enable(isInvalid)
    gl.drawArrays(TRIANGLES, 0, size)
    disable()
}

fun VertexBuffer.renderLineLoop(isInvalid: Boolean = true, isSmooth: Boolean = false) {
    enable(isInvalid)
    if (isSmooth) gl.enable(LINE_SMOOTH)
    gl.drawArrays(LINE_LOOP, 0, size)
    if (isSmooth) gl.disable(LINE_SMOOTH)
    disable()
}

fun VertexBuffer.renderLines(isInvalid: Boolean = true, isSmooth: Boolean = false) {
    enable(isInvalid)
    if (isSmooth) gl.enable(LINE_SMOOTH)
    gl.drawArrays(LINES, 0, size)
    if (isSmooth) gl.disable(LINE_SMOOTH)
    disable()
}

fun VertexBuffer.renderLineStrip(isInvalid: Boolean = true, isSmooth: Boolean = false) {
    enable(isInvalid)
    if (isSmooth) gl.enable(LINE_SMOOTH)
    gl.drawArrays(LINE_STRIP, 0, size)
    if (isSmooth) gl.disable(LINE_SMOOTH)
    disable()
}

@Constructor
fun Component.VertexBuffer(attributes: List<VertexAttribute>, capacity: Int, vertexCount: Int): VertexBuffer =
    VertexBuffer(import(), attributes, capacity, vertexCount)

@Constructor
fun Shader.VertexBuffer(stride: Int): VertexBuffer =
    VertexBuffer(import(), attributes, 0, stride)


fun Graphics.bindAttributes(program: GLProgram, attributes: List<VertexAttribute>) {
    attributes.forEachIndexed { index, attribute ->
        gl.bindAttribLocation(program, index, attribute.name)
    }
}
