package featurea.runtime

import kotlin.reflect.KClass

typealias ModuleBlock = (Module) -> Unit

class ModuleNotFoundException(canonicalName: String) : RuntimeException(canonicalName)

class Module(val runtimeBuilder: RuntimeBuilder, val container: Container) {

    lateinit var key: String

    val components = ComponentRegistry<Any>()
    internal val propertyRegistry = PropertyRegistry<Any>()
    internal var isCreate: Boolean = false

    inline fun <reified T : Any> importComponent(): T =
        importComponent(T::class)

    fun <T : Any> delegateOf(type: KClass<out Proxy<T>>): T =
        importComponent(type).delegate

    fun <T : Any> importComponent(type: KClass<T>): T {
        val canonicalName = container.dependencyRegistry.findCanonicalName(type)
        return importComponent(type, canonicalName)
    }

    inline fun <reified T : Any> importComponent(canonicalName: String): T {
        val type = T::class
        return importComponent(type, canonicalName)
    }

    fun <T : Any> importComponent(type: KClass<T>, canonicalName: String): T {
        // 1. existing component
        val existingComponent = importComponentOrNull(type, canonicalName)
        if (existingComponent != null) {
            return existingComponent
        }

        // 2. new component
        components.pushTransaction(canonicalName)
        val component = loadComponent(type, canonicalName)
        components.pullTransaction(component)
        if (component is Component) {
            component.onCreateComponent()
        }
        installPlugin(component::class)
        return component
    }

    inline fun <reified T : Any> createComponent(noinline init: T.() -> Unit = {}): T {
        val canonicalName = container.dependencyRegistry.findCanonicalName(T::class)
        return createComponent(canonicalName, init)
    }

    inline fun <reified T : Any> createComponent(canonicalName: String, init: T.() -> Unit = {}): T {
        val component = loadComponent(T::class, canonicalName)
        component.init()
        if (component is Component) {
            component.onCreateComponent()
        }
        return component
    }

    fun <T : Any> provideComponent(component: T) {
        val type = component::class
        val canonicalName = container.dependencyRegistry.findCanonicalName(type)
        components.inject(canonicalName, component)
        installPlugin(type)
    }

    fun <T : Any> importComponentOrNull(type: KClass<T>, canonicalName: String): T? {
        val existingComponent = components[canonicalName] as T?
        if (existingComponent != null) {
            return existingComponent
        }

        val implicitComponent = container.findStaticOrNull<T>(canonicalName)
        if (implicitComponent != null) {
            return implicitComponent
        }

        val include = runtimeBuilder.moduleProvider.findIncludeOrNull(canonicalName)
        if (include != null) {
            return include.importComponent(type, canonicalName)
        }

        return null
    }

    fun <T : Any> loadComponent(type: KClass<T>, canonicalName: String): T {
        val dependencyRegistry = container.dependencyRegistry
        val componentConstructor = dependencyRegistry.moduleComponents[canonicalName]
        if (componentConstructor != null) {
            // 1. create component
            try {
                val component = componentConstructor(this)
                return component as T
            } catch (e: Exception) {
                e.printStackTrace()
                throw e
            }
        } else {
            // 2. provide proxy
            val proxy = type
            val componentProvider = dependencyRegistry.componentProviders[proxy]
            if (componentProvider == null) {
                throw DependencyNotFoundException(canonicalName)
            }
            importComponent<Component>(componentProvider)
            val existingComponent = components[canonicalName] ?: throw DependencyNotFoundException(canonicalName)
            return existingComponent as T
        }
    }

    fun destroy() {
        container.removeModule(this)
    }

    /*internals*/

    internal fun onDestroy() {
        for (destroyModuleBlock in runtimeBuilder.destroyModuleBlocks) {
            destroyModuleBlock(this)
        }
        runtimeBuilder.moduleProvider.builder.onDestroyBlock?.invoke(this)
        for (component in components) {
            if (component is Component) {
                component.onDestroyComponent()
            }
        }
    }

    internal fun installPlugin(plugin: KClass<*>) {
        val features = container.dependencyRegistry.features[plugin]
        if (features != null) {
            for (feature in features) {
                importComponent<Component>(feature)
            }
        }
    }

    override fun toString(): String = "Module($key)"

}

@Constructor
fun Module(init: ModuleBuilder.() -> Unit): ModuleBuilder = ModuleBuilder().apply(init)

@Constructor
fun DefaultModule() = Module {}

/*convenience*/

val Module.artifact: Dependency get() = container.artifact

inline fun <reified T : Any> Module.findComponent(): T {
    val type = T::class
    val canonicalName = container.dependencyRegistry.findCanonicalName(type)
    val existingComponent: T? = components[canonicalName] as T?
    if (existingComponent != null) {
        return existingComponent
    } else {
        throw DependencyNotFoundException(type)
    }
}
