package featurea.text

import featurea.System
import featurea.text.reader.readTextFileOrNull

class TextContent(val system: System) {

    private val existingTextMap = mutableMapOf<String, String>()

    suspend fun findTextOrNull(filePath: String, limit: Int = -1): String? {
        val existingText = existingTextMap[filePath]
        if (existingText != null) {
            return existingText
        }
        val text = system.readTextFileOrNull(filePath, limit)
        if (text != null) {
            existingTextMap[filePath] = text
        }
        return text
    }

    fun removeCachedTextFile(filePath: String) {
        existingTextMap.remove(filePath)
    }

    fun clearCache() {
        existingTextMap.clear()
    }

}
