package featurea.texture

import featurea.findIndexBySum
import featurea.replaceWith
import featurea.runtime.Module
import featurea.runtime.Component
import featurea.runtime.import
import featurea.splitAndTrim
import featurea.content.Resource
import featurea.texture.reader.*
import featurea.runtime.Constructor

class SpriteSequence(override val module: Module) : Component {

    private lateinit var resource: Resource
    private val textureAssetLoader = import<TextureLoader>()
    private val textureAtlasCache = import<TextureAtlasCache>()

    private val frameDurations = mutableListOf<Float>()
    var maxTime: Float = 0f
        private set
    var maxFrameIndex: Int = 0
        private set
    var maxLoopCount: Int = 0
        private set
    val isNotLoop: Boolean get() = maxLoopCount == 1
    val isInfinite: Boolean get() = maxLoopCount == 0

    private var currentTime: Float = 0f
    private var currentFrameIndex: Int = 0
    private var currentLoopCount: Int = 0
    private var isRunning: Boolean = false
    lateinit var currentTexturePath: String
    var currentTexture: Texture? = null
        private set
    var currentTextureRegion: TextureRegion? = null
        private set
    val hasTexture: Boolean get() = currentTexture != null && currentTextureRegion != null

    fun init(resource: Resource) {
        this.resource = resource
        // 2. init
        frameDurations.replaceWith(resource.manifest.fps.splitAndTrim(",").map { 1000f / it.toFloat() })
        maxTime = frameDurations.sum()
        maxLoopCount = resource.manifest.loopCount
        maxFrameIndex = resource.manifest.frameCount - 1

        // 3. validate
        check(maxTime > 0)
        check(maxLoopCount >= 0)
        check(maxFrameIndex >= 0)
    }

    fun start() {
        currentTime = 0f
        currentFrameIndex = 0
        currentLoopCount = 0
        resume()
    }

    fun resume() {
        if (isRunning) return
        isRunning =
            isInfinite || currentLoopCount < maxLoopCount || currentFrameIndex < maxFrameIndex || currentTime < maxTime
    }

    fun pause() {
        isRunning = false
    }

    fun stop() {
        pause()
        currentTime = maxTime
        currentFrameIndex = maxFrameIndex
        currentLoopCount = maxLoopCount
    }

    fun reset() {
        currentTime = 0f
        currentFrameIndex = 0
        currentLoopCount = 0
        isRunning = false
        currentTexture = null
        currentTextureRegion = null
    }

    fun updateTexture(frameTime: Float, invalidate: () -> Unit = {}) {
        // 1. currentTime, currentFrameIndex, currentLoopCount
        if (isRunning) {
            currentTime += frameTime
            if (currentTime >= maxTime) currentLoopCount++
            currentTime %= maxTime
            currentFrameIndex = frameDurations.findIndexBySum(currentTime)
        }

        // 2. isRunning
        if (!isInfinite) {
            if (currentLoopCount >= maxLoopCount) stop()
        }

        // 3. currentTexture, currentTextureRegion
        currentTexturePath = resource.manifest.frames[currentFrameIndex]
        val texture = textureAssetLoader.findTextureOrNull(currentTexturePath)
        val textureRegion = textureAtlasCache.textureRegionMap[currentTexturePath]
        if (texture != currentTexture || textureRegion != currentTextureRegion) {
            invalidate()
        }
        currentTexture = texture
        currentTextureRegion = textureRegion
    }

}

@Constructor
fun Component.SpriteSequence() = SpriteSequence(module)