package featurea.script

import featurea.divide
import featurea.formula.Formula
import featurea.splitWithWrappers

sealed class ScriptOperator(val shell: Shell, val value: String) {
    abstract suspend fun execute(scriptCall: ScriptCall): Any?
}

class ScriptRightOperator(shell: Shell, value: String) : ScriptOperator(shell, value) {

    val key: String
    private val action: String?
    private val argsString: String?
    private val result = ScriptResult()

    init {
        val actionId: String
        val firstIndexOfOpenBrace = value.indexOf('(')
        val lastIndexOfCloseBrace = value.lastIndexOf(')')
        if (firstIndexOfOpenBrace != -1 && lastIndexOfCloseBrace == value.lastIndex) {
            actionId = value.substring(0, firstIndexOfOpenBrace)
            this.argsString = value.substring(firstIndexOfOpenBrace + 1, value.lastIndex)
        } else {
            actionId = value
            this.argsString = null
        }
        // >> quickfix todo improve
        val lastDotIndex = actionId.lastIndexOf(".")
        if (lastDotIndex != -1) {
            this.key = actionId.substring(0, lastDotIndex)
            this.action = actionId.substring(lastDotIndex + 1)
        } else {
            this.key = value
            this.action = null
        }
        // <<
    }

    override suspend fun execute(scriptCall: ScriptCall): Any? {
        val stackFrame = scriptCall.data
        val argsList = evaluateArguments(argsString, scriptCall)
        run /*1. action*/ {
            if (action != null && argsString != null) {
                val importKey = scriptCall.imports[key] ?: key
                val script = stackFrame.localVariables[importKey] as Script? ?: scriptCall.docket.getScript(importKey)
                if (script != null) {
                    return script.executeAction(action, argsList)
                }
            }
        }
        run /*2. local variable from stack*/ {
            val localVariable = stackFrame.localVariables[value]
            if (localVariable != null) {
                return localVariable
            }
        }
        run /*3. primitive value*/ {
            if (result.assignPrimitiveValue(value)) return result.value
        }
        run /*4. argument*/ {
            if (stackFrame.hasArguments) {
                return stackFrame.args.pop()
            }
        }
        /*5. execute config*/
        /*run  {
            if (key.endsWith("Config")) {
                val configName = key.removeSuffix("Config")
                val configValue = shell.contentCache.configCache.getValue("$configName:$action")
                if (configValue != null) {
                    val configImports = shell.contentCache.scriptCache.importsOf(configName)
                    return shell.execute(configImports, scriptRoutine.docket, configValue, stackFrame)
                }
            }
        }*/
        run /*6. do math*/{
            return evaluateMath(value, scriptCall)
        }
    }

    /*internals*/

    private suspend fun evaluateArguments(argsString: String?, scriptCall: ScriptCall): Args {
        if (argsString == null) {
            return emptyList()
        }
        val argsList = argsString.splitWithWrappers(',')
        return argsList.map { ScriptRightOperator(shell, it).execute(scriptCall) }
    }

    private fun <T> evaluateMath(math: String, scriptCall: ScriptCall): T {
        val value = run {
            var result = math
            for ((localVariableName, localVariable) in scriptCall.data.localVariables) {
                result = result.replace(localVariableName, localVariable.toString())
            }
            result
        }
        val formula = Formula<T>(value)
        return formula.calculate()
    }

}

class ScriptLeftOperator(shell: Shell, value: String) : ScriptOperator(shell, value) {

    override suspend fun execute(scriptCall: ScriptCall): Any? {
        val stackFrame = scriptCall.data
        val result = stackFrame.result
        if (value.contains("var ")) {
            // 1. create new local variable in stack
            val varName = value.removePrefix("var ")
            varName.validateVarName()
            stackFrame.localVariables[varName] = result
        } else if (stackFrame.localVariables.containsKey(value)) {
            // 2. existing local variable
            stackFrame.localVariables[value] = result
        } else {
            // 3. setter action
            val (key, action) = value.divide(".")
            if (action == null) {
                throw IllegalArgumentException("value: $value")
            }
            val importKey = scriptCall.imports[key] ?: key
            val script = scriptCall.docket.getScript(importKey) ?: throw DocketNotFoundException(value)
            script.executeAction(action, args = listOf(result))
        }
        return result
    }

}

/*internals*/

private fun String.validateVarName() {
    if (contains(".")) throw ScriptSyntaxException("Illegal var name because contains dot symbol: $this")
}

private fun String.isImportDirective(): Boolean = startsWith("import ") && contains(" as ")
