package featurea.script

import featurea.Stack

class ScriptCall(val shell: Shell, val imports: Imports, val docket: Docket, val script: String, val data: StackFrame) {

    constructor(shell: Shell, script: String, docket: Docket, args: Args, imports: Map<String, String>) :
            this(shell, imports, docket, script, StackFrame(Stack(args.reversed())))

    suspend fun <T : Any?> execute(): T {
        val expressions = shell.parseScript(script)
        for (index in 0 until expressions.lastIndex) {
            expressions[index].execute(this)
        }
        return expressions.last().execute(this) as T
    }

    suspend fun eval(): String {
        var result = script
        val matchResults = expressionRegex.findAll(result)
        for (matchResult in matchResults) {
            val script = matchResult.value.subSequence(2, matchResult.value.lastIndex).toString()
            val executeResult = shell.execute<Any>(imports, docket, script, data)
            result = result.replaceFirst(matchResult.value, executeResult.toString())
        }
        return result
    }

}
