package learnopengl

import featurea.color.Color
import featurea.log
import featurea.math.Matrix
import featurea.math.Vector
import featurea.opengl.*
import featurea.shader.ShaderSource
import featurea.shader.offsetOf
import featurea.shader.readShaderSource
import featurea.shader.stride

fun Test.loadTexture(program: GLProgram, slot: Int, name: String, texture: String) {
    val textureId = textureProvider.findTextureOrNull(texture)?.id

    // glsl: texture -> slot 0
    gl.uniform1i(gl.getUniformLocation(program, name), slot) // texture -> slot 0
    // kotlin: texture -> slot 0
    gl.activeTexture(TEXTURE0 + slot) // -> slot 0
    gl.bindTexture(TEXTURE_2D, textureId) // texture ->

    gl.texParameteri(TEXTURE_2D, TEXTURE_WRAP_S, textureWrap.s)
    gl.texParameteri(TEXTURE_2D, TEXTURE_WRAP_T, textureWrap.t)
    gl.texParameteri(TEXTURE_2D, TEXTURE_MIN_FILTER, LINEAR)
    gl.texParameteri(TEXTURE_2D, TEXTURE_MAG_FILTER, LINEAR)
}

suspend fun Test.loadShaderOrNull(shaderPath: String): Pair<GLProgram, ShaderSource>? {
    // shader
    val shaderSource = textContent.readShaderSource(shaderPath)

    /*
    log(shaderSource.vs)
    log("--")
    log(shaderSource.ps)
    */

    val vertexShader = gl.createShader(VERTEX_SHADER) ?: return null
    gl.shaderSource(vertexShader, shaderSource.vs)
    gl.compileShader(vertexShader)
    if (gl.getShaderiv(vertexShader, COMPILE_STATUS) != TRUE) log(gl.getShaderInfoLog(vertexShader))

    val fragmentShader = gl.createShader(FRAGMENT_SHADER) ?: return null
    gl.shaderSource(fragmentShader, shaderSource.ps)
    gl.compileShader(fragmentShader)
    if (gl.getShaderiv(fragmentShader, COMPILE_STATUS) != TRUE) log(gl.getShaderInfoLog(fragmentShader))

    val program = gl.createProgram() ?: return null
    gl.attachShader(program, vertexShader)
    gl.attachShader(program, fragmentShader)
    gl.linkProgram(program)
    if (gl.getProgramiv(program, LINK_STATUS) != TRUE) log(gl.getProgramInfoLog(program))

    for ((index, attribute) in shaderSource.attributes.withIndex()) {
        attribute.location = gl.getAttribLocation(program, attribute.name) ?: continue
        attribute.offset = shaderSource.attributes.offsetOf(index)
    }

    gl.deleteShader(vertexShader)
    gl.deleteShader(fragmentShader)

    return program to shaderSource
}

fun GLES20.bindAttributes(shaderSource: ShaderSource) {
    for (attr in shaderSource.attributes) {
        enableVertexAttribArray(attr.location)
        vertexAttribPointer(attr.location, attr.size, FLOAT, false, shaderSource.attributes.stride, attr.offset)
    }
}

fun GLES20.setUniform(program: GLProgram, name: String, color: Color) {
    val (red, green, blue, alpha) = color
    uniform4f(getUniformLocation(program, name), red, green, blue, alpha)
}

fun GLES20.setUniform(program: GLProgram, name: String, f1: Float, f2: Float, f3: Float) {
    uniform3f(getUniformLocation(program, name), f1, f2, f3)
}

fun GLES20.setUniform(program: GLProgram, name: String, vector: Vector) {
    val (x, y, z) = vector
    uniform3f(getUniformLocation(program, name), x, y, z)
}

fun GLES20.setUniform(program: GLProgram, name: String, float: Float) {
    uniform1f(getUniformLocation(program, name), float)
}

fun GLES20.setUniform(program: GLProgram, name: String, matrix: Matrix) {
    uniformMatrix4fv(getUniformLocation(program, name), false, matrix)
}

fun GLES20.bindArrayBufferShader(arrayBuffer: GLArrayBuffer, program: GLProgram, source: ShaderSource) {
    useProgram(program)
    bindBuffer(ARRAY_BUFFER, arrayBuffer)
    bindAttributes(source)
}

fun GLES20.bindArrayBufferData(arrayBuffer: GLArrayBuffer?, data: FloatArray, usage: Int) {
    bindBuffer(ARRAY_BUFFER, arrayBuffer)
    bufferData(ARRAY_BUFFER, data, usage)
}

fun GLES20.clearColorBuffer(red: Float, green: Float, blue: Float, alpha: Float) {
    clearColor(red, green, blue, alpha)
    clear(COLOR_BUFFER_BIT)
}

fun GLES20.clearColorBufferAndDepthBuffer(red: Float, green: Float, blue: Float, alpha: Float) {
    clearColor(red, green, blue, alpha)
    enable(DEPTH_TEST)
    clear(COLOR_BUFFER_BIT or DEPTH_BUFFER_BIT)
}

fun matrix(init: Matrix.() -> Unit) = MATRIX.apply { assignIdentity(); init() }

/*internals*/

private val MATRIX: Matrix = Matrix()
