package learnopengl

import featurea.Application
import featurea.ApplicationDelegate
import featurea.color.toColor
import featurea.content.Content
import featurea.graphics.Graphics
import featurea.input.Input
import featurea.keyboard.Keyboard
import featurea.loader.loadResources
import featurea.math.Vector
import featurea.math.toVector
import featurea.opengl.FRONT_AND_BACK
import featurea.opengl.GLES20
import featurea.runtime.Component
import featurea.runtime.Module
import featurea.runtime.import
import featurea.script.Docket
import featurea.script.Script
import featurea.text.TextContent
import featurea.texture.TextureLoader
import learnopengl.types.*

abstract class Test(override val module: Module) : Component, ApplicationDelegate, Script {

    private var isBuild: Boolean = false

    val app = import<Application>()
    val content = import<Content>()
    val keyboard = import<Keyboard>()
    val input = import<Input>()
    val graphics = import<Graphics>()
    val gl = import<GLES20>()
    val textContent = import<TextContent>()
    val textureProvider = import<TextureLoader>()

    val blendFunc = BlendFunc()
    val textureFilter = TextureFilter()
    val textureWrap = TextureWrap()
    var diffuseTexture: String? = null
    var specularTexture: String? = null
    var emissionTexture: String? = null
    var alphaTest: Float = 0.1f
    var alphaTest2: Float = 0.1f
    var rotationAngle: Double = 0.0
    var rotationIncrement: Float = 0f
    var rotationAxis: Vector = Vector()
    var viewTranslation: Vector = Vector()
    var near: Float = 1f
    var far: Float = 100f
    var fov: Float = 45f
    var radius: Float = 10.0f

    init {
        content.requiredResources.addAll(learnopengl.requiredResources)
    }

    abstract suspend fun load()

    override val docket: Docket = Docket { parentResult, action, args ->
        when (action) {
            "clearColor" -> {
                val (red, green, blue, alpha) = args[1].toString().toColor()
                gl.clearColor(red, green, blue, alpha)
            }
            "blendFunc.sfactor" -> blendFunc.sfactor = blendFuncOf(args[1].toString())
            "blendFunc.dfactor" -> blendFunc.dfactor = blendFuncOf(args[1].toString())
            "blendFunc.color" -> blendFunc.color = args[1].toString().toColor()
            "polygonMode" -> {
                val mode = polygonModeOf(args[1].toString())
                gl.polygonMode(FRONT_AND_BACK, mode)
            }
            "diffuseTexture" -> diffuseTexture = args[1].toString()
            "specularTexture" -> specularTexture = args[1].toString()
            "texture.minFilter" -> textureFilter.minFilter = textureMinFilterOf(args[1].toString())
            "texture.magFilter" -> textureFilter.magFilter = textureMagFilterOf(args[1].toString())
            "texture.wrapS" -> textureWrap.s = textureWrapOf(args[1].toString())
            "texture.wrapT" -> textureWrap.t = textureWrapOf(args[1].toString())
            "alphaTest" -> alphaTest = args[1].toString().toFloat()
            "alphaTest2" -> alphaTest2 = args[1].toString().toFloat()
            "rotationAngle" -> rotationAngle = args[1].toString().toDouble()
            "rotationIncrement" -> rotationIncrement = args[1].toString().toFloat()
            "viewTranslation" -> viewTranslation = args[1].toString().toVector()
            "rotationAxis" -> rotationAxis = args[1].toString().toVector()
            "near" -> near = args[1].toString().toFloat()
            "far" -> far = args[1].toString().toFloat()
            "fov" -> fov = args[1].toString().toFloat()
            "radius" -> radius = args[1].toString().toFloat()
            "build" -> {
                val diffuseTexture = diffuseTexture
                if (diffuseTexture != null) loadResources(diffuseTexture)
                val specularTexture = specularTexture
                if (specularTexture != null) loadResources(specularTexture)

                if (!isBuild) {
                    isBuild = true
                    loading(1f) // quickfix todo improve
                }
            }
        }
        this@Test
    }

}
