package featurea.script

import featurea.runtime.Module

typealias ActionBlock<T> = suspend T.(parentResult: Any?, action: String, args: Args) -> Any?

class DocketNotFoundException(message: String) : RuntimeException(message)

interface Docket {
    val parent: Docket? get() = null
    suspend fun executeAction(action: String, args: Args = args()): Any?
    suspend fun getScript(key: String): Script? = executeAction(key, emptyList()) as Script?
}

fun <T : Any> T.Docket(parent: Docket? = null, block: ActionBlock<T>): Docket {
    return object : Docket {
        override val parent = parent
        override suspend fun executeAction(action: String, args: Args): Any? {
            val parentResult = parent?.executeAction(action, args)
            return block(parentResult, action, args)
        }
    }
}

fun Module.toDocket(): Docket = object : Docket {
    override suspend fun executeAction(action: String, args: Args): Any = importComponent(action)
}
