package learnopengl

import featurea.input.InputEvent
import featurea.input.InputEventType
import featurea.input.InputListener
import featurea.keyboard.KeyEvent
import featurea.keyboard.KeyEventSource
import featurea.keyboard.KeyEventType
import featurea.keyboard.KeyListener
import featurea.math.Matrix
import featurea.math.Point
import featurea.math.Vector
import featurea.math.aspectRatio
import featurea.opengl.*
import featurea.runtime.Module
import featurea.shader.ShaderSource
import learnopengl.Resources.test3dShader

fun CameraTest(module: Module): Test = object : Test(module), InputListener, KeyListener {

    private val projection = Matrix()
    private val camera: TestCamera = TestCamera(pos = Vector(0f, 0f, 3f))
    private val mouse: Point = Point(-1f, -1f)

    private lateinit var arrayBuffer: GLArrayBuffer
    private lateinit var shaderProgram: GLProgram
    private lateinit var shaderSource: ShaderSource

    override suspend fun load() {
        arrayBuffer = gl.createBuffer() ?: return
        gl.bindArrayBufferData(arrayBuffer, cubeVerticesWithUvs, STATIC_DRAW)
        val (program, shaderSource) = loadShaderOrNull(test3dShader) ?: return
        this.shaderProgram = program
        this.shaderSource = shaderSource
    }

    override fun update(elapsedTime: Float) {
        gl.clearColorBufferAndDepthBuffer(0.2f, 0.3f, 0.3f, 1.0f)
        gl.bindArrayBufferShader(arrayBuffer, shaderProgram, shaderSource)
        diffuseTexture?.also { loadTexture(shaderProgram, 0, "texture", it) }
        projection.assignPerspective(near, far, camera.zoom, graphics.surface.size.aspectRatio)
        gl.setUniform(shaderProgram, "projection", projection)
        gl.setUniform(shaderProgram, "view", camera.viewMatrix())
        for ((index, position) in cubePositions.withIndex()) {
            gl.setUniform(shaderProgram, "model", matrix {
                translate(position)
                rotate(1.0f, 0.3f, 0.5f, angle = 20.0 * index)
            })
            gl.drawArrays(TRIANGLES, 0, 36)
        }
    }

    init {
        input.addListener(this)
        keyboard.addListener(this)
    }

    override fun onInput(inputEvent: InputEvent) {
        val x = inputEvent.x2
        val y = inputEvent.y2
        if (inputEvent.type == InputEventType.DRAG) {
            camera.ProcessMouseMovement(x - mouse.x, mouse.y - y)
        }
        mouse.assign(x, y)
    }

    override fun onKey(keyEvent: KeyEvent) {
        if (keyEvent.type == KeyEventType.PRESS) {
            when (keyEvent.source) {
                KeyEventSource.W -> camera.ProcessKeyboard(TestCameraMovement.FORWARD, app.elapsedTime)
                KeyEventSource.S -> camera.ProcessKeyboard(TestCameraMovement.BACKWARD, app.elapsedTime)
                KeyEventSource.A -> camera.ProcessKeyboard(TestCameraMovement.LEFT, app.elapsedTime)
                KeyEventSource.D -> camera.ProcessKeyboard(TestCameraMovement.RIGHT, app.elapsedTime)
            }
        }
    }

}

fun testCamera(module: Module): Test = CameraTest(module).apply {
    diffuseTexture = "images/smile.png"
    textureWrap.s = CLAMP_TO_BORDER
    textureWrap.t = CLAMP_TO_BORDER
    near = 0.1f
    far = 100f
    fov = 45f
}
