package featurea.window

import featurea.System
import featurea.SystemPropertyDelegate
import featurea.runtime.Artifact
import featurea.runtime.DependencyBuilder

/*dependencies*/

expect fun DependencyBuilder.includeExternals()

val artifact = Artifact("featurea.window") {
    includeExternals()
    include(featurea.graphics.artifact)
    include(featurea.script.artifact)
    include(featurea.loader.artifact)

    "Window" to ::Window
    "WindowContainer" to ::WindowContainer
    "WindowDelegate" to ::WindowDelegate
    "WindowLoop" to ::WindowLoop
    "WindowModule" to ::WindowModule
}

/*manifest*/

val System.alwaysOnTop: Boolean by SystemPropertyDelegate("alwaysOnTop") { false }
