package featurea.runtime

import kotlin.reflect.KClass

@Repeatable
@Retention(AnnotationRetention.SOURCE)
annotation class Provide(val proxy: KClass<out Proxy<*>>)

enum class ProvideType {
    INJECT,
    APPEND,
    REPLACE,
}

object UnitProxy : Proxy<Unit> {
    override val delegate: Unit = Unit
}

interface Proxy<T> {
    val delegate: T
}

interface ComponentProvider<T> {
    fun provideComponent(module: Module)
}

@Constructor
fun <T : Any> ComponentProvider(init: ComponentProvider<T>.(module: Module) -> Unit): ComponentProvider<T> {
    return object : ComponentProvider<T> {
        override fun provideComponent(module: Module) = init(module)
    }
}
