package featurea.runtime

class ModuleProvider(val type: ProvideType, val canonicalName: String, val builder: ModuleBuilder) {

    val includes = mutableMapOf<String, Module>()

    fun include(module: Module) {
        includes[module.key.packageId] = module
    }

    fun includeAll(modules: Iterable<Module>) {
        for (module in modules) include(module)
    }

    /*internals*/

    internal fun provideModule(runtimeBuilder: RuntimeBuilder, container: Container): Module {
        when (type) {
            ProvideType.INJECT -> {
                val existingModule = container.modules[canonicalName]
                if (existingModule != null) return existingModule

                val newModule = Module(runtimeBuilder, container)
                container.injectModule(canonicalName, newModule)
                return newModule
            }
            ProvideType.APPEND -> {
                val newModule = Module(runtimeBuilder, container)
                container.appendModule(canonicalName, newModule)
                return newModule
            }
            ProvideType.REPLACE -> {
                TODO()
            }
        }
    }

    internal fun findIncludeOrNull(canonicalName: String): Module? = includes[canonicalName.packageId]

}

/*internals*/

private val String.packageId: String
    get() {
        val lastIndexOfDot = lastIndexOf(".")
        if (lastIndexOfDot == -1) return ""
        return substring(0, lastIndexOfDot)
    }
