package featurea.opengl

import featurea.math.Matrix
import featurea.math.copyToArray16
import featurea.runtime.Component
import featurea.runtime.Module
import org.khronos.webgl.*

actual class GLES20 actual constructor(override val module: Module) : Component {

    private val floatArray16 = FloatArray(size = 16)

    lateinit var glContext: WebGLRenderingContext

    actual fun activeTexture(texture: Int) {
        glContext.activeTexture(texture)
    }

    actual fun createProgram(): GLProgram? {
        val id: WebGLProgram? = glContext.createProgram()
        if (id == null) {
            return null
        }
        return GLProgram(id)
    }

    actual fun bindAttribLocation(program: GLProgram, index: Int, name: String) {
        glContext.bindAttribLocation(program.id, index, name)
    }

    actual fun attachShader(program: GLProgram, shader: GLShader) {
        glContext.attachShader(program.id, shader.id)
    }

    actual fun linkProgram(program: GLProgram) {
        glContext.linkProgram(program.id)
    }

    actual fun getProgramiv(program: GLProgram, pname: Int): Int {
        return glContext.getProgramParameter(program.id, pname).toIntCode()
    }

    actual fun getShaderiv(shader: GLShader, pname: Int): Int {
        return glContext.getShaderParameter(shader.id, pname).toIntCode()
    }

    actual fun shaderSource(shader: GLShader, source: String) {
        glContext.shaderSource(shader.id, source)
    }

    actual fun compileShader(shader: GLShader) {
        glContext.compileShader(shader.id)
    }

    actual fun getProgramInfoLog(program: GLProgram): String? {
        return glContext.getProgramInfoLog(program.id)
    }

    actual fun getShaderInfoLog(shader: GLShader): String? {
        return glContext.getShaderInfoLog(shader.id)
    }

    actual fun useProgram(program: GLProgram?) {
        glContext.useProgram(program?.id)
    }

    actual fun enableVertexAttribArray(index: Int) {
        glContext.enableVertexAttribArray(index)
    }

    actual fun vertexAttribPointer(index: Int, size: Int, type: Int, normalized: Boolean, stride: Int, offset: Int) {
        glContext.vertexAttribPointer(index, size, type, normalized, stride, offset)
    }

    actual fun disableVertexAttribArray(index: Int) {
        glContext.disableVertexAttribArray(index)
    }

    actual fun uniformMatrix4fv(location: GLUniformLocation, transpose: Boolean, matrix: Matrix) {
        glContext.uniformMatrix4fv(location.id, transpose, matrix.copyToArray16(floatArray16) as Float32Array)
    }

    actual fun uniform1f(location: GLUniformLocation, f1: Float) {
        glContext.uniform1f(location.id, f1)
    }

    actual fun uniform1i(location: GLUniformLocation, i1: Int) {
        glContext.uniform1i(location.id, i1)
    }

    actual fun uniform2f(location: GLUniformLocation, f1: Float, f2: Float) {
        glContext.uniform2f(location.id, f1, f2)
    }

    actual fun uniform3f(location: GLUniformLocation, f1: Float, f2: Float, f3: Float) {
        glContext.uniform3f(location.id, f1, f2, f3)
    }

    actual fun uniform4f(location: GLUniformLocation, f1: Float, f2: Float, f3: Float, f4: Float) {
        glContext.uniform4f(location.id, f1, f2, f3, f4)
    }

    actual fun getUniformLocation(program: GLProgram, name: String): GLUniformLocation {
        val id: WebGLUniformLocation? = glContext.getUniformLocation(program.id, name)
        return GLUniformLocation(id)
    }

    actual fun getAttribLocation(program: GLProgram, name: String): Int {
        val id: Int = glContext.getAttribLocation(program.id, name)
        return id
    }

    actual fun drawArrays(mode: Int, first: Int, count: Int) {
        glContext.drawArrays(mode, first, count)
    }

    actual fun drawElements(mode: Int, count: Int, type: Int, indices: IntArray) {
        glContext.drawElements(mode, count, type, 0)
    }

    actual fun createTexture(textureRegionPath: String): GLTexture? {
        val id: WebGLTexture? = glContext.createTexture()
        if (id == null) {
            return null
        }
        return GLTexture(id)
    }

    actual fun enable(capability: Int) {
        if (hasCapability(capability)) {
            glContext.enable(capability)
        }
    }

    actual fun cullFace(mode: Int) {
        glContext.cullFace(mode)
    }

    actual fun blendFunc(sfactor: Int, dfactor: Int) {
        glContext.blendFunc(sfactor, dfactor)
    }

    actual fun blendFuncSeparate(srcRGB: Int, dstRGB: Int, srcAlpha: Int, dstAlpha: Int) {
        glContext.blendFuncSeparate(srcRGB, dstRGB, srcAlpha, dstAlpha)
    }

    actual fun blendColor(red: Float, green: Float, blue: Float, alpha: Float) {
        glContext.blendColor(red, green, blue, alpha)
    }

    actual fun blendEquation(mode: Int) {
        glContext.blendEquation(mode)
    }

    actual fun bindTexture(target: Int, texture: GLTexture?) {
        glContext.bindTexture(target, texture?.id)
    }

    actual fun texParameteri(target: Int, name: Int, value: Int) {
        glContext.texParameteri(target, name, value)
    }

    actual fun generateMipmap(target: Int) {
        glContext.generateMipmap(target)
    }

    actual fun disable(capability: Int) {
        if (hasCapability(capability)) {
            glContext.disable(capability)
        }
    }

    actual fun deleteTexture(texture: GLTexture) {
        glContext.deleteTexture(texture.id)
    }

    actual fun createBuffer(): GLArrayBuffer? {
        val id: WebGLBuffer? = glContext.createBuffer()
        if (id == null) {
            return null
        }
        return GLArrayBuffer(id)
    }

    actual fun deleteBuffer(buffer: GLArrayBuffer) {
        glContext.deleteBuffer(buffer.id)
    }

    actual fun bindBuffer(target: Int, buffer: GLArrayBuffer?) {
        glContext.bindBuffer(target, buffer?.id)
    }

    actual fun bufferData(target: Int, data: FloatArray, usage: Int) {
        glContext.bufferData(target, data as BufferDataSource, usage)
    }

    actual fun bufferData(target: Int, data: IntArray, usage: Int) {
        glContext.bufferData(target, data as BufferDataSource, usage)
    }

    actual fun viewport(x: Int, y: Int, width: Int, height: Int) {
        glContext.viewport(x, y, width, height)
    }

    actual fun clear(mask: Int) {
        glContext.clear(mask)
    }

    actual fun clearColor(red: Float, green: Float, blue: Float, alpha: Float) {
        glContext.clearColor(red, green, blue, alpha)
    }

    actual fun depthFunc(func: Int) {
        glContext.depthFunc(func)
    }

    actual fun createShader(type: Int): GLShader? {
        val id: WebGLShader? = glContext.createShader(type)
        if (id == null) {
            return null
        }
        return GLShader(id)
    }

    actual fun deleteShader(shader: GLShader) {
        glContext.deleteShader(shader.id)
    }

    actual fun scissor(x: Int, y: Int, width: Int, height: Int) {
        glContext.scissor(x, y, width, height)
    }

    actual fun lineWidth(width: Float) {
        glContext.lineWidth(width)
    }

    actual fun pixelStorei(pname: Int, param: Int) {
        glContext.pixelStorei(pname, param)
    }

    actual fun polygonMode(face: Int, mode: Int) {
        // only for desktop targets
    }

}

/*internals*/

private val skippedCapabilities = listOf(TEXTURE_2D)

private fun hasCapability(capability: Int) = !skippedCapabilities.contains(capability)

private fun Any?.toIntCode(): Int = when (this) {
    null -> 0
    is Boolean -> if (this) 1 else 0
    is Number -> toInt()
    else -> -1
}
