package featurea.shader

import featurea.color.Color
import featurea.graphics.Camera
import featurea.graphics.Graphics
import featurea.graphics.coordinates
import featurea.math.*
import featurea.opengl.*
import featurea.runtime.Module
import featurea.runtime.Component
import featurea.runtime.import
import featurea.shader.Uniforms.Pool.point0
import featurea.shader.Uniforms.Pool.point1

// https://stackoverflow.com/a/16622177/909169
class Uniforms(private val shader: Shader) : Component {

    override val module: Module = shader.module
    private val graphics = import<Graphics>()
    private val gl = import<GLES20>()

    private val locationMap = mutableMapOf<String, GLUniformLocation>()
    private val viewScale: Vector2 = Vector2(1f, 1f)
    val viewProjectionMatrix: Matrix = Matrix()
    val viewMatrix: Matrix = Matrix()
    val modelMatrix: Matrix = Matrix()

    fun invalidate(camera: Camera, init: Uniforms.() -> Unit = {}) =
        invalidate(camera.coordinates, init)

    fun invalidate(coordinates: Coordinates?, init: Uniforms.() -> Unit = {}) {
        init()
        val coordinates = if (graphics.useCameras) coordinates else graphics.surface.coordinates
        if (coordinates != null) {
            val transform = coordinates()
            viewMatrix.assign(transform.matrix)
            viewProjectionMatrix.assignMultiplication(graphics.surface.matrix, viewMatrix)
            viewScale.assign(transform.sx, transform.sy)
        } else {
            viewMatrix.assign(graphics.surface.matrix)
            viewScale.assign(1f, 1f)
        }
    }

    fun update(init: Uniforms.() -> Unit = {}) {
        init()
        set("MODEL_MATRIX", modelMatrix)
        set("VIEW_MATRIX", viewMatrix)
        set("PROJECTION_MATRIX", graphics.surface.matrix)
        set("VIEW_PROJECTION_MATRIX", viewProjectionMatrix)
        set("VIEW_SCALE", viewScale)
        set("WINDOW_SIZE", graphics.surface.size)
    }

    operator fun set(name: String, b1: Boolean) {
        return gl.uniform1i(findLocation(name), if (b1) 1 else 0)
    }

    operator fun set(name: String, i1: Int) {
        return gl.uniform1i(findLocation(name), i1)
    }

    operator fun set(name: String, f1: Float) {
        return gl.uniform1f(findLocation(name), f1)
    }

    operator fun set(name: String, f1: Float, f2: Float) {
        return gl.uniform2f(findLocation(name), f1, f2)
    }

    operator fun set(name: String, f1: Float, f2: Float, f3: Float) {
        return gl.uniform3f(findLocation(name), f1, f2, f3)
    }

    operator fun set(name: String, f1: Float, f2: Float, f3: Float, f4: Float) {
        return gl.uniform4f(findLocation(name), f1, f2, f3, f4)
    }

    operator fun set(name: String, c: Color) {
        return gl.uniform4f(findLocation(name), c.red, c.green, c.blue, c.alpha)
    }

    operator fun set(name: String, matrix: Matrix4) {
        return gl.uniformMatrix4fv(findLocation(name), false, matrix)
    }

    operator fun set(name: String, vector: Vector2) {
        return gl.uniform2f(findLocation(name), vector.x, vector.y) // quickfix todo
    }

    operator fun set(name: String, size: Size) {
        return gl.uniform2f(findLocation(name), size.width, size.height)
    }

    operator fun set(name: String, rect: Rectangle) {
        return gl.uniform4f(findLocation(name), rect.x1, rect.y1, rect.x2, rect.y2)
    }

    operator fun set(name: String, textureUnit: TextureUnit) {
        gl.uniform1i(findLocation(name), textureUnit.slot)
        gl.activeTexture(TEXTURE0 + textureUnit.slot)
        gl.bindTexture(TEXTURE_2D, textureUnit.id)
        gl.texParameteri(TEXTURE_2D, TEXTURE_MIN_FILTER, textureUnit.min)
        gl.texParameteri(TEXTURE_2D, TEXTURE_MAG_FILTER, textureUnit.mag)
        gl.texParameteri(TEXTURE_2D, TEXTURE_WRAP_S, textureUnit.clamp.first)
        gl.texParameteri(TEXTURE_2D, TEXTURE_WRAP_T, textureUnit.clamp.second)
    }

    /*internals*/

    private fun findLocation(name: String): GLUniformLocation {
        val existingLocation = locationMap[name]
        if (existingLocation != null) return existingLocation

        val location = gl.getUniformLocation(shader.id, name)
        locationMap[name] = location
        return location
    }

    internal object Pool {

        val point0: Point get() = POINTS[0].assign(0f, 0f)
        val point1: Point get() = POINTS[1].assign(0f, 0f)
        private val POINTS: Array<Point> = Array(size = 2) { Point() }

    }

}

fun Uniforms.mv(point: Point): Point = mv(point.x, point.y)

// libs/shader/res/featurea/shader/default.c:16
fun Uniforms.mv(x: Float, y: Float): Point = (viewMatrix * (modelMatrix * point0.assign(x, y)))

fun Uniforms.mvp(point: Point): Point = mvp(point.x, point.y)

// libs/shader/res/featurea/shader/default.c:20
fun Uniforms.mvp(x: Float, y: Float): Point = (viewProjectionMatrix * (modelMatrix * point1.assign(x, y)))
