package featurea.runtime

import kotlin.reflect.KMutableProperty
import kotlin.reflect.KProperty

class Property(val receiver: Any, val name: String) {
    override fun hashCode(): Int = receiver.hashCode()
    override fun equals(other: Any?): Boolean = other is Property && other.receiver === receiver && other.name == name
}

fun <T> Component.property(reference: KMutableProperty<T>, init: () -> T): PropertyRegistry<T> =
    module.propertyRegistry.apply { initProperty(this@property, reference, init()) } as PropertyRegistry<T>

fun <T> Component.property(reference: KProperty<T>): PropertyRegistry<T> =
    module.propertyRegistry as PropertyRegistry<T>

fun registerPropertyListener(receiver: Component, reference: KProperty<*>, listener: () -> Unit) =
    receiver.module.propertyRegistry.registerListener(receiver, reference, listener)

@Deprecated("drop concept", ReplaceWith("module.properties.updatePropertyListeners(this, property)"))
fun Component.updateProperty(reference: KProperty<*>) = module.propertyRegistry.updateListeners(this, reference)
