package featurea.config

import featurea.*
import featurea.content.*
import featurea.runtime.Container
import kotlin.text.endsWith

class ConfigReader(container: Container) : ResourceReader(container, CONFIG_CONTENT_TYPE) {

    private val configContent = container.import<ConfigContent>()

    override suspend fun readResourceOrNull(resourcePath: String, isBundle: Boolean): Resource? {
        if (resourcePath.extension == PROPERTIES) {
            val configPath = findConfigPath(resourcePath) ?: return null
            val name = configPath.pathWithoutExtension
            val config = configContent.findConfig(name)
            val resources = config.files.map { it.path }
            return Resource(resources) {
                Properties("configPath" to configPath)
            }
        }
        return null
    }

    /*internals*/

    private fun findConfigPath(value: String): String? {
        return if (value.endsWith("-desktop.properties")) {
            if (targetOs != TargetOs.DESKTOP) return null
            value.replace("-desktop.properties", ".properties")
        } else {
            value
        }
    }

}

/*properties*/

val Resource.configPath: String by ResourcePropertyDelegate("configPath") { error("configPath") }
