package learnopengl.types

import featurea.color.Color
import featurea.color.Colors.transparentColor
import featurea.opengl.*

class BlendFunc {
    var sfactor: Int = ONE
    var dfactor: Int = ZERO
    var color: Color = transparentColor
}

fun blendFuncOf(value: String): Int = when (value) {
    "ZERO" -> ZERO
    "ONE" -> ONE
    "SRC_COLOR" -> SRC_COLOR
    "ONE_MINUS_SRC_COLOR" -> ONE_MINUS_SRC_COLOR
    "DST_COLOR" -> DST_COLOR
    "ONE_MINUS_DST_COLOR" -> ONE_MINUS_DST_COLOR
    "SRC_ALPHA" -> SRC_ALPHA
    "ONE_MINUS_SRC_ALPHA" -> ONE_MINUS_SRC_ALPHA
    "DST_ALPHA" -> DST_ALPHA
    "ONE_MINUS_DST_ALPHA" -> ONE_MINUS_DST_ALPHA
    "CONSTANT_COLOR" -> CONSTANT_COLOR
    "ONE_MINUS_CONSTANT_COLOR" -> ONE_MINUS_CONSTANT_COLOR
    "CONSTANT_ALPHA" -> CONSTANT_ALPHA
    "ONE_MINUS_CONSTANT_ALPHA" -> ONE_MINUS_CONSTANT_ALPHA
    "SRC_ALPHA_SATURATE" -> SRC_ALPHA_SATURATE
    else -> error("value: $value")
}
