package featurea.shader

import featurea.opengl.*

typealias Attributes = List<VertexAttribute>

class VertexAttribute(val name: String, val size: Int) {
    var location: Int = -1
    var offset: Int = -1
}

val Attributes.vertexSize get() = sumBy { it.size }

val Attributes.stride get() = vertexSize * Float.SIZE_BYTES

fun Attributes.offsetOf(location: Int): Int {
    if (location == 0) return 0
    var result = 0
    for (index in 1..location) {
        result += this[index - 1].size * Float.SIZE_BYTES
    }
    return result
}

fun GLES20.enableArrayBuffer(arrayBuffer: GLArrayBuffer, attributes: Attributes, data: FloatArray, isInvalid: Boolean) {
    bindBuffer(ARRAY_BUFFER, arrayBuffer)
    if (isInvalid) {
        bufferData(ARRAY_BUFFER, data, STATIC_DRAW)
    }
    for (attribute in attributes) {
        enableVertexAttribArray(attribute.location)
        vertexAttribPointer(attribute.location, attribute.size, FLOAT, false, attributes.stride, attribute.offset)
    }
}

fun GLES20.disableArrayBuffer(attributes: Attributes) {
    for (attribute in attributes) {
        disableVertexAttribArray(attribute.location)
    }
    bindBuffer(ARRAY_BUFFER, null)
}
