package featurea.runtime

typealias Containers = ContainerProvider.() -> Unit

typealias Modules = ModuleProvider.() -> Unit

class RuntimeBuilder internal constructor() {

    lateinit var containerProvider: ContainerProvider
    lateinit var moduleProvider: ModuleProvider
    internal val initModuleBlocks = mutableListOf<ModuleBlock>()
    internal val buildModuleBlocks = mutableListOf<ModuleBlock>()
    internal val destroyModuleBlocks = mutableListOf<ModuleBlock>()

    /*container API*/

    fun initContainer(container: Container) {
        this.containerProvider = ContainerProvider(container).apply {
            initDependencyRegistry(container.dependencyRegistry)
        }
    }

    fun injectContainer(artifact: Dependency, canonicalName: String, containerRegistry: ContainerRegistry = ContainerRegistry(), init: Containers = {}) =
        init(ProvideType.INJECT, artifact, canonicalName, containerRegistry, init)

    fun injectDefaultContainer(artifact: Dependency, containerRegistry: ContainerRegistry = ContainerRegistry(), init: Containers = {}) =
        init(ProvideType.INJECT, artifact, "featurea.runtime.DefaultContainer", containerRegistry, init)

    fun appendContainer(artifact: Dependency, canonicalName: String, containerRegistry: ContainerRegistry = ContainerRegistry(), init: Containers = {}) =
        init(ProvideType.APPEND, artifact, canonicalName, containerRegistry, init)

    fun replaceContainer(artifact: Dependency, canonicalName: String, containerRegistry: ContainerRegistry = ContainerRegistry(), init: Containers = {}) =
        init(ProvideType.REPLACE, artifact, canonicalName, containerRegistry, init)

    private fun init(type: ProvideType, artifact: Dependency, canonicalName: String, containerRegistry: ContainerRegistry, init: Containers) {
        val isDefaultContainer = canonicalName.contains(".Default")
        val dependencyRegistry = DependencyRegistry.fromDependency(artifact)
        val containerBuilder = if (isDefaultContainer) DefaultContainer()
        else dependencyRegistry.containers[canonicalName]?.invoke() ?:
        /*throw ContainerNotFoundException(canonicalName)*/ DefaultContainer() // quickfix because of external editors todo uncomment
        this.containerProvider = ContainerProvider(type, canonicalName, containerBuilder, containerRegistry).apply {
            initDependencyRegistry(dependencyRegistry)
            init()
        }
    }

    /*module API*/

    fun injectModule(canonicalName: String, init: Modules = {}) =
        create(ProvideType.INJECT, canonicalName, init)

    fun injectDefaultModule(init: Modules = {}) =
        create(ProvideType.INJECT, "featurea.runtime.DefaultModule", init)

    fun appendModule(canonicalName: String, init: Modules = {}) =
        create(ProvideType.APPEND, canonicalName, init)

    fun appendDefaultModule(init: Modules = {}) =
        create(ProvideType.APPEND, "featurea.runtime.DefaultModule", init)

    fun replaceModule(canonicalName: String, init: Modules = {}) =
        create(ProvideType.REPLACE, canonicalName, init)

    private fun create(type: ProvideType, canonicalName: String, init: Modules) {
        val isDefaultModule = canonicalName.contains(".Default")
        val dependencyRegistry = containerProvider.dependencyRegistry
        val moduleBuilder = if (isDefaultModule) DefaultModule()
        else dependencyRegistry.modules[canonicalName]?.invoke() ?:
        /*throw ModuleNotFoundException(canonicalName)*/ DefaultModule() // quickfix because of external editors todo uncomment
        this.moduleProvider = ModuleProvider(type, canonicalName, moduleBuilder).apply(init)
    }

    fun init(block: ModuleBlock) {
        initModuleBlocks.add(block)
    }

    fun build(block: ModuleBlock) {
        buildModuleBlocks.add(block)
    }

    fun destroy(block: ModuleBlock) {
        destroyModuleBlocks.add(block)
    }

}
