package featurea.script

import featurea.runtime.Component
import featurea.runtime.Module

class Shell {

    val actions = mutableMapOf<String, ScriptFunction>()

    suspend fun <T : Any?> execute(imports: Imports = imports(), docket: Docket, code: String, args: Args = args()): T =
        ScriptCall(this, code, docket, args, imports).execute()

    suspend fun <T : Any?> execute(imports: Imports = imports(), module: Module, code: String, data: StackFrame): T =
        execute(imports, module.toDocket(), code, data)

    suspend fun <T : Any?> execute(imports: Imports = imports(), docket: Docket, code: String, data: StackFrame): T =
        ScriptCall(this, imports, docket, code, data).execute()

    suspend fun eval(code: String, docket: Docket, args: Args = args(), imports: Imports = imports()) =
        ScriptCall(this, code, docket, args, imports).eval()

}

suspend fun <T : Any?> Component.executeScript(script: String, vararg args: Any? = emptyArray()): T =
    module.executeScript(script, *args) // just for debug todo revert to `args`

suspend fun <T : Any?> Module.executeScript(script: String, vararg args: Any? = emptyArray()): T {
    val shell = importComponent<Shell>()
    return shell.execute(imports(), toDocket(), script, listOf(*args))
}
