package featurea

open class Stack<T> {

    private val elements: MutableList<T> = mutableListOf()

    constructor()

    constructor(elements: Collection<T>) : this() {
        this.elements.addAll(elements)
    }

    constructor(vararg elements: T) : this() {
        this.elements.addAll(elements)
    }

    fun isEmpty() = elements.isEmpty()

    fun isNotEmpty() = elements.isNotEmpty()

    fun push(item: T) {
        elements.add(item)
    }

    fun pop(): T {
        val item = elements.removeAt(elements.lastIndex)
        return item
    }

    fun popOrNull(): T? {
        if (elements.isEmpty()) return null
        return pop()
    }

    fun last(): T = elements[elements.size - 1]

    fun lastOrNull(): T? {
        if (isEmpty()) return null
        return elements[elements.size - 1]
    }

    val size: Int get() = elements.size

    fun clear() = elements.clear()

}
