package featurea.layout

val PortraitOrientations = listOf(Orientation.Portrait, Orientation.PortraitUpsideDown)

val LandscapeOrientations = listOf(Orientation.LandscapeLeft, Orientation.LandscapeRight)

val AllOrientations = listOf(
    Orientation.Portrait,
    Orientation.PortraitUpsideDown,
    Orientation.LandscapeRight,
    Orientation.LandscapeLeft
)

enum class Orientation {

    Portrait, PortraitUpsideDown, LandscapeRight, LandscapeLeft;

    val isVertical get() = this == Portrait || this == PortraitUpsideDown
    val isHorizontal get() = this == LandscapeRight || this == LandscapeLeft

}
