package featurea.cli

import featurea.StringBlock

typealias Options = Map<String, String>

suspend fun launchCommand(command: String, options: Options = emptyMap(), workingDir: String?, log: StringBlock): Int =
    runCommand(command, command, workingDir, options, -1, log)

suspend fun runCommand(command: String, name: String? = null, log: StringBlock = {}): Int =
    runCommand(command, name, workingDir = null, options = emptyMap(), timeout = 600_000L, log)

expect suspend fun runCommand(
    command: String,
    name: String? = null,
    workingDir: String?,
    options: Options,
    timeout: Long,
    log: StringBlock
): Int

fun parseCommand(args: Array<String>, run: (name: String, options: Array<String>) -> Unit) {
    check(args.isNotEmpty()) { "no args provided" }
    val name = args[0]
    val options = if (args.size == 1) emptyArray() else args.copyOfRange(1, args.size)
    return run(name, options)
}
