package featurea.window

import featurea.runtime.*

expect fun WindowContainer(): ContainerBuilder

expect fun WindowModule(): ModuleBuilder

fun Artifact.WindowRuntime(init: ComponentBlock) = Runtime {
    val artifact = this@WindowRuntime
    injectContainer(artifact.dependencies, "featurea.window.WindowContainer")
    injectModule("featurea.window.WindowModule")
    build { module -> init(module.importComponent()) }
}

fun WindowRuntime(simulatorModule: Module, artifact: Dependency, onBuild: ModuleBlock) = Runtime {
    val simulatorContainer = simulatorModule.container
    appendContainer(artifact, "featurea.window.DefaultWindowContainer", simulatorContainer.registry) {
        include(simulatorContainer)
    }
    injectModule("featurea.window.DefaultWindowModule")
    init { it.importComponent<Window>() }
    build { module -> onBuild(module) }
}
