package featurea.runtime

open class ComponentRegistry<T> : Iterable<T> {

    private val components = LinkedHashMap<String, T>()
    private var transactionKey: String? = null
    private var keyIndex: Long = 0

    operator fun get(key: String): T? = components[key]

    operator fun get(key: String, defaultValue: T): T = components[key] ?: defaultValue

    override fun iterator(): Iterator<T> = components.values.iterator()

    fun append(key: String, component: T) {
        keyIndex++
        val componentKey = "$key$keyIndex"
        inject(componentKey, component)
        if (component is Module) component.key = componentKey
        if (component is Container) component.key = componentKey
    }

    fun inject(key: String, component: T) {
        if (containsKey(key)) error("Duplicated component key: $key, $component")
        components[key] = component
    }

    fun remove(key: String): T? = components.remove(key)

    fun containsKey(key: String): Boolean = components.keys.contains(key)

    fun contains(component: T): Boolean = components.values.contains(component)

    fun pushTransaction(key: String) {
        this.transactionKey = key
    }

    fun pullTransaction(component: T) {
        val transactionKey = transactionKey ?: error("push transaction first")
        if (components[transactionKey] == null) {
            components[transactionKey] = component
        }
    }

}
