package featurea.rml

import featurea.content.ResourceTag
import featurea.splitAndTrim

fun ResourceTag.startObject(rmlResource: RmlResource?) =
    rmlResource?.startObjectByRmlTag?.get(this) ?: throw RuntimeException()

fun ResourceTag.endObject(rmlResource: RmlResource?) =
    rmlResource?.endObjectByRmlTag?.get(this) ?: throw RuntimeException()

fun ResourceTag.indexOfSelf(): Int = parent?.indexOf(this) ?: -1

fun ResourceTag.findResource(): String? = if (isRoot) filePath else "${parent?.findResource()}:/${id}"

fun ResourceTag.isLeaf(): Boolean = properties.isEmpty() && children.isEmpty()

fun ResourceTag.findAttribute(key: String): String? {
    val value = attributes[key]
    if (value != null) return value
    val link = findLinkOrNull()
    if (link != null) return link.findAttribute(key)
    return null
}

fun ResourceTag.findLinkOrNull(): ResourceTag? {
    val link = attributes["link"] ?: return null
    val linkRmlTag = (if (link.startsWith("/")) {
        rootRmlTag.properties[link]
    } else {
        parent!!.properties[link] ?: rootRmlTag.properties[link]
    }) ?: return null
    check(!linkRmlTag.containsRecursively(this)) { "Recursive links not allowed" }
    return linkRmlTag
}

fun ResourceTag.deepCopy(parent: ResourceTag? = this.parent): ResourceTag {
    val copy = ResourceTag(name, parent, filePath)
    for ((key, value) in attributes) {
        copy.attributes[key] = value
    }
    for ((key, property) in properties) {
        copy.properties[key] = property
    }
    for (child in children) {
        copy.children.add(child.deepCopy(parent = copy))
    }
    return copy
}

fun ResourceTag.getPropertyByIdPath(idPath: String?): ResourceTag? {
    if (idPath == null) return this
    var currentProperty: ResourceTag = this
    val idArray = idPath.splitAndTrim("/")
    for (id in idArray) {
        currentProperty = currentProperty.properties[id] ?: return null
    }
    return currentProperty
}

fun ResourceTag.containsRecursively(child: ResourceTag): Boolean {
    var ancestor: ResourceTag? = child.parent
    while (true) {
        if (ancestor == null) return false
        if (ancestor == this) return true
        ancestor = ancestor.parent
    }
}

fun ResourceTag.findParent(predicate: (parent: ResourceTag) -> Boolean): ResourceTag? {
    var currentParent: ResourceTag? = parent
    while (currentParent != null) {
        if (predicate(currentParent)) return currentParent
        currentParent = currentParent.parent
    }
    return null
}

fun ResourceTag.anyParent(predicate: (parent: ResourceTag) -> Boolean): Boolean = findParent(predicate) != null

suspend fun ResourceTag.forEachResourceAttribute(action: suspend (rmlTag: ResourceTag, key: String, value: String) -> Unit) {
    for ((key, value) in attributes) {
        action(this, key, value)
    }
    for (childRmlTag in children) {
        childRmlTag.forEachResourceAttribute(action)
    }
}

fun ResourceTag.selectionPathRelativeTo(parentRmlTag: ResourceTag): IntArray {
    val list = ArrayList<Int>()
    var currentRmlTag = this
    while (currentRmlTag.parent != null && currentRmlTag !== parentRmlTag) {
        list.add(currentRmlTag.parent!!.indexOf(currentRmlTag))
        currentRmlTag = currentRmlTag.parent!!
    }
    val listIndices = IntArray(list.size)
    list.reverse()
    for (i in list.indices) {
        listIndices[i] = list[i]
    }
    return listIndices
}

fun ResourceTag.renameId(newId: String) {
    if (newId == id) return

    val parentRmlTag = parent!!
    val currentId = id

    parentRmlTag.properties.remove(currentId)
    parentRmlTag.children.removeAll { it.id == currentId }

    attributes["id"] = newId

    parentRmlTag.properties[newId] = this
    parentRmlTag.children.add(this)
}
