package featurea.window

import featurea.color.Colors.whiteColor
import featurea.graphics.Graphics
import featurea.js.*
import featurea.js.dialog.titlebarHeight
import featurea.js.dialog.titlebarOrNull
import featurea.log
import featurea.runtime.*
import kotlinx.browser.document
import kotlinx.coroutines.GlobalScope
import kotlinx.coroutines.launch
import org.w3c.dom.HTMLCanvasElement
import org.w3c.dom.HTMLElement
import kotlin.math.max
import kotlin.math.roundToInt
import kotlinx.browser.window as jsWindow

@Provide(HtmlElementProxy::class)
class MainView(override val module: Module) : Component {

    private val appLoop = import<WindowLoop>()
    private val graphics = import<Graphics>()
    private val window = import<Window>()

    private val rootElement: HTMLElement = import<RootElementProxy>().delegate
    private val mainCanvas: HTMLCanvasElement = rootElement.querySelector("#mainCanvas") as HTMLCanvasElement
    private val dialogContent: HTMLElement? = rootElement.querySelector(".content") as HTMLElement?
    private val hasDialogContent: Boolean = dialogContent != null

    private var isDestroy: Boolean = false
    private var currentWidth = -1
    private var currentHeight = -1
    private var then: Float = 0f

    override fun onCreateComponent() {
        graphics.init()
        if (hasDialogContent) {
            mainCanvas.setHtmlCanvasSize(rootElement.clientWidth, rootElement.clientHeight - rootElement.titlebarHeight)
        }
        dialogContent?.onchange = /*draggable-resizable-dialog.js:228*/ {
            window.resizeLayout()
        }
        provideProxy(HtmlElementProxy(mainCanvas))
        jsWindow.requestAnimationFrame {
            onUpdate(it)
        }
        val titlebar = rootElement.titlebarOrNull
        if (titlebar != null) {
            log("[MainView] titlebar: $titlebar")
            titlebar.addEventListener("dblclick", {
                val mainCanvas: HTMLCanvasElement = rootElement.querySelector("#mainCanvas") as HTMLCanvasElement
                keep(mainCanvas)
                js(
                    """
                    if (mainCanvas.requestFullscreen) {
                        mainCanvas.requestFullscreen();
                    } else if (mainCanvas.mozRequestFullScreen) { /* Firefox */
                        mainCanvas.mozRequestFullScreen();
                    } else if (mainCanvas.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
                        mainCanvas.webkitRequestFullscreen();
                    } else if (mainCanvas.msRequestFullscreen) { /* IE/Edge */
                        mainCanvas.msRequestFullscreen();
                    }                    
                    """
                )
            })
        }
    }

    override fun onDestroyComponent() {
        isDestroy = true
    }

    /*internals*/

    private fun onUpdate(now: Double) {
        if (isDestroy) return

        val now = now.toFloat()
        val elapsedTime = now - then
        then = now
        GlobalScope.launch {
            onResize()
            if (!hasDialogContent) {
                graphics.clear(whiteColor) // quickfix todo improve
            }
            appLoop.onTick(elapsedTime)
            jsWindow.requestAnimationFrame {
                onUpdate(it)
            }
        }
    }

    private fun onResize() {
        if (hasDialogContent) {
            val (glWidth, glHeight) = if (document.isFullscreen) {
                val jsScreen = jsWindow.screen
                if (currentWidth == jsScreen.width && currentHeight == jsScreen.height) return
                currentWidth = jsScreen.width
                currentHeight = jsScreen.height
                mainCanvas.setHtmlCanvasSize(currentWidth, currentHeight)
            } else {
                if (currentWidth == rootElement.clientWidth && currentHeight == rootElement.clientHeight) return
                currentWidth = rootElement.clientWidth
                currentHeight = rootElement.clientHeight
                mainCanvas.setHtmlCanvasSize(currentWidth, currentHeight - rootElement.titlebarHeight)
            }
            graphics.resize(glWidth, glHeight)
            window.resizeLayout()
        } else {
            // >>
            // 1)
            /*
            if (currentWidth == mainCanvas.width && currentHeight == mainCanvas.height) return
            currentWidth = mainCanvas.width
            currentHeight = mainCanvas.height
            graphics.resize(currentWidth, currentHeight)
            window.resizeLayout()
            */

            // 2)
            if (currentWidth == jsWindow.innerWidth && currentHeight == jsWindow.innerHeight) return
            currentWidth = jsWindow.innerWidth
            currentHeight = jsWindow.innerHeight
            var gw: Int = (currentWidth * 0.75).roundToInt()
            gw = max(480, gw) // quickfix todo improve
            val gh = (max(CURRENT_WIDTH, CURRENT_HEIGHT).toFloat() * 540 / 1680f).roundToInt() // quickfix todo improve
            // val gh: Int = (currentHeight * 0.8f).roundToInt()
            val (glWidth, glHeight) = mainCanvas.setHtmlCanvasSize(gw, gh)
            graphics.resize(glWidth, glHeight)
            window.resizeLayout()
            // <<
        }
    }

}
