package featurea.texture.reader

import featurea.Properties
import featurea.PropertyDelegate
import featurea.System
import featurea.SystemPropertyDelegate
import featurea.runtime.Artifact

/*dependencies*/

val artifact = Artifact("featurea.texture.reader") {
    include(featurea.text.artifact)

    "TextureAtlasCache" to TextureAtlasCache::class
    "TexturePackReader" to TexturePackReader::class
    "TextureReader" to TextureReader::class

    static {
        provideComponent(TextureAtlasCache(container = this))
        provideComponent(TexturePackReader(container = this))
        provideComponent(TextureReader(container = this))
    }
}

/*properties*/

var Properties.texture: String by PropertyDelegate("texture") { error("texture") }
var Properties.atlas: String by PropertyDelegate("atlas") { error("atlas") }
var Properties.pack: String by PropertyDelegate("pack") { error("pack") }
var Properties.frameCount: Int by PropertyDelegate("frameCount") { error("frameCount") }
var Properties.frames: List<String> by PropertyDelegate("frames") { error("frames") }
var Properties.fps: String by PropertyDelegate("fps") { error("fps") }
var Properties.loopCount: Int by PropertyDelegate("loopCount") { 0 }
var System.useTexturePack: Boolean by SystemPropertyDelegate("useTexturePack") { false }
