package featurea.runtime

import kotlin.native.concurrent.ThreadLocal

@ThreadLocal
private var transactionRuntime: Runtime? = null

fun launchRuntime(receiver: Proxy<*> = UnitProxy, init: RuntimeBuilder.() -> Unit = {}) =
    launch(receiver) { Runtime(init) }

fun launch(receiver: Proxy<*> = UnitProxy, init: Runtime.() -> RuntimeBuilder) {
    check(transactionRuntime == null) { "current transaction not completed" }
    val runtime = Runtime(receiver).apply {
        onInitSuccess {
            transactionRuntime = null
        }
        onInitFailure {
            transactionRuntime = null
        }
    }
    transactionRuntime = runtime
    runtime.onInit(init)
}

fun provideProxy(proxy: Proxy<*>) {
    val runtime: Runtime = transactionRuntime ?: throw RuntimeNotFoundException()
    runtime.provideProxy(proxy)
}
