package featurea.script

import featurea.toMap

class ScriptFunction(private val shell: Shell, private val value: String, private val argNames: List<String>) {

    suspend fun execute(args: List<Any>, docket: Docket): Any? {
        val stackFrame = StackFrame(argNames toMap args)
        return shell.execute(docket = docket, code = value, data = stackFrame)
    }

}
