package featurea.texture.reader

import featurea.*
import featurea.cli.runCommand
import featurea.content.*
import featurea.runtime.Container
import featurea.text.TextContent

const val TEXTURE_CONTENT_TYPE = "texture"

class TextureReader(container: Container) : ResourceReader(container, TEXTURE_CONTENT_TYPE) {

    private val system = container.import<System>()
    private val textContent = container.import<TextContent>()
    private val textureAtlasCache = container.import<TextureAtlasCache>()
    private val textPackReader = container.import<TexturePackReader>()
    private val missingManifestCache = mutableSetOf<String>()

    override suspend fun instrumentResource(resourcePath: String) {
        if (resourcePath.isValidFilePath() && resourcePath.extension == GIF) {
            val absolutePath = system.findAbsolutePathOrNull(resourcePath)
            val resourceDir = "$GIF_CACHE_PATH/${resourcePath.normalizedPath.removePrefix("/")}"
            if (!system.existsFile(resourceDir)) {
                runCommand("texture extractGif '$absolutePath' '$resourcePath'", name = "Loading GIF file...")
            }
        }
    }

    override suspend fun readResourceOrNull(resourcePath: String, isBundle: Boolean): Resource? {
        if (system.useTexturePack) {
            textPackReader.init {
                for ((textureAtlasPath, textureAtlas) in textureAtlasCache.textureAtlasMap) {
                    for ((textureRegionPath, textureRegion) in textureAtlas.textureRegions) {
                        textureAtlasCache.textureRegionMap[textureRegionPath] = textureRegion
                    }
                }
            }
        }
        if (resourcePath.hasExtension(PNG, JPG, JPEG) && resourcePath.isValidFilePath()) {
            if (system.useTexturePack) {
                val textureAtlas = textureAtlasCache.textureRegionMap[resourcePath]?.atlas
                if (textureAtlas != null) {
                    return Resource(textureAtlas.path, TEXTURES_PACK_PATH) {
                        Properties("atlas" to textureAtlas.path, "pack" to TEXTURES_PACK_PATH)
                    }
                } else {
                    return null
                }
            } else {
                return Resource(resourcePath) {
                    Properties("texture" to resourcePath)
                }
            }
        }
        if (resourcePath.extension == GIF && resourcePath.isValidFilePath()) {
            val resourceDir = "$GIF_CACHE_PATH/${resourcePath.normalizedPath.removePrefix("/")}"
            val manifestPath = "${resourceDir}/manifest.properties"
            val properties = Properties().apply {
                val textOrNull = textContent.findTextOrNull(manifestPath)
                val properties = if (textOrNull != null) parseProperties(textOrNull) else {
                    if (missingManifestCache.add(manifestPath)) throw ResourceNotFoundException(manifestPath)
                    mutableMapOf(
                        "frameCount" to 0,
                        "frames" to emptyList<String>(),
                        "fps" to 60,
                        "loopCount" to 1
                    ) // just for try
                }
                entries.putAll(properties)
            }
            val resources = mutableListOf<String>()
            for (index in 0 until properties.frameCount) {
                resources.add("${resourceDir}/${index}.png")
            }
            if (system.useTexturePack) {
                val frames = ArrayList<String>().apply {
                    add(TEXTURES_PACK_PATH)
                    addAll(resources.map { textureAtlasCache.getTextureAtlas(it).path })
                }
                return Resource(frames, manifestPath) { properties }
            } else {
                return Resource(resources, manifestPath) { properties }
            }
        }
        return null
    }

}
