package featurea.content

import featurea.hasExtension
import featurea.isValidFilePath

const val PNG: String = "png"
const val MP3: String = "mp3"
const val JPG: String = "jpg"
const val JPEG: String = "jpeg"
const val GIF: String = "gif"
const val PROPERTIES: String = "properties"
const val SHADER: String = "shader"

/*convenience*/

fun String.isValidImageResource(): Boolean = isValidFilePath() && hasExtension(PNG, JPG, JPEG, GIF)

fun String.isValidAudioResource(): Boolean = isValidFilePath() && hasExtension(MP3)
