package featurea.graphics

import featurea.js.HTMLCanvasElementProxy
import featurea.js.dynamicMapOf
import featurea.opengl.GLES20
import featurea.runtime.Module
import org.khronos.webgl.WebGLRenderingContext
import org.w3c.dom.HTMLCanvasElement

// https://stackoverflow.com/a/39354174/909169
fun WebglProvider(module: Module) {
    val mainCanvas: HTMLCanvasElement = module.importComponent<HTMLCanvasElementProxy>().delegate
    val gl = module.importComponent<GLES20>()
    gl.glContext = mainCanvas.getContext("webgl", dynamicMapOf("alpha" to false)) as WebGLRenderingContext
}
