package featurea.config

import featurea.content.CONFIG_CONTENT_TYPE
import featurea.content.Resource
import featurea.content.ResourceLoader
import featurea.content.ResourceTag
import featurea.pathWithoutExtension
import featurea.runtime.Module
import featurea.runtime.import

class ConfigLoader(module: Module) : ResourceLoader(module, CONFIG_CONTENT_TYPE) {

    private val configContent = import<ConfigContent>()

    override fun findResourcesOrNull(resourceTag: ResourceTag, key: String, value: String): List<String>? = null

    override suspend fun loadingResource(resource: Resource) {
        val name = resource.configPath.pathWithoutExtension
        configContent.findConfig(name)
        resource.loadingQueue.add(resource.path)
    }

    override suspend fun releaseResource(resource: Resource) {
        // todo
        resource.releaseQueue.add(resource.path)
    }

}
