package featurea.runtime

class RuntimeNotFoundException : RuntimeException()

class Runtime(val receiver: Any) {

    private var state: RuntimeState = RuntimeState.RUNTIME_NEW

    private lateinit var builder: RuntimeBuilder
    private lateinit var container: Container
    private lateinit var module: Module

    private val containerBuilder by lazy { builder.containerProvider.builder }
    private val moduleBuilder by lazy { builder.moduleProvider.builder }
    private val awaitProxiesInContainer by lazy { containerBuilder.awaitProxies.toMutableList() }
    private val awaitProxiesInModule by lazy { ArrayList(moduleBuilder.awaitProxies) }

    private val initContainerBlocks = mutableListOf<() -> Unit>()
    private val initModuleBlocks = mutableListOf<ModuleBlock>()
    private lateinit var onSuccess: () -> Unit
    private lateinit var onFailure: () -> Unit

    fun initContainer(block: () -> Unit) {
        initContainerBlocks.add(block)
    }

    fun initModule(block: ModuleBlock) {
        initModuleBlocks.add(block)
    }

    fun onInitSuccess(onSuccess: () -> Unit) {
        this.onSuccess = onSuccess
    }

    fun onInitFailure(onFailure: () -> Unit) {
        this.onFailure = onFailure
    }

    fun onInit(init: Runtime.() -> RuntimeBuilder) {
        try {
            state = RuntimeState.RUNTIME_INIT_START
            builder = init()
            container = builder.containerProvider.providerContainer(builder)
            module = builder.moduleProvider.provideModule(builder, container)

            state = RuntimeState.CONTAINER_INIT_START
            if (!container.isCreate) {
                for (initContainerBlock in initContainerBlocks) initContainerBlock()
            }

            state = RuntimeState.CONTAINER_INIT_COMPLETE
            if (!container.isCreate) {
                containerBuilder.onInitBlock?.invoke(container)
            }

            if (state == RuntimeState.CONTAINER_INIT_COMPLETE) createContainer()
        } catch (e: Exception) {
            e.printStackTrace()
            onFailure()
        }
    }

    private fun createContainer() {
        try {
            if (awaitProxiesInContainer.isEmpty()) {
                state = RuntimeState.CONTAINER_CREATE
                if (!container.isCreate) {
                    containerBuilder.onCreateBlock?.invoke(container)
                }
                container.isCreate = true

                state = RuntimeState.MODULE_INIT
                if (!module.isCreate) {
                    for (component in container.components) module.installPlugin(plugin = component::class)
                    for (initModuleBlock in initModuleBlocks) initModuleBlock(module)
                    for (initModuleBlock in builder.initModuleBlocks) initModuleBlock(module)
                    moduleBuilder.onInitBlock?.invoke(moduleBuilder.InitBlock(), module)
                }
                createModule()
            } else {
                state = RuntimeState.CONTAINER_CREATE_AWAIT
            }
        } catch (e: Exception) {
            e.printStackTrace()
            onFailure()
        }
    }

    private fun createModule() {
        try {
            if (awaitProxiesInModule.isEmpty()) {
                state = RuntimeState.MODULE_CREATE
                if (!module.isCreate) {
                    moduleBuilder.onCreateBlock?.invoke(module)
                }

                state = RuntimeState.MODULE_BUILD
                for (buildModuleBlock in builder.buildModuleBlocks) buildModuleBlock(module)
                module.isCreate = true

                state = RuntimeState.RUNTIME_INIT_COMPLETE
                onSuccess()
            } else {
                state = RuntimeState.MODULE_CREATE_AWAIT
            }
        } catch (e: Exception) {
            e.printStackTrace()
            onFailure()
        }
    }

    fun provideProxy(proxy: Proxy<*>) {
        try {
            val type = proxy::class
            if (awaitProxiesInContainer.contains(type)) {
                awaitProxiesInContainer.remove(type)
                container.provideComponent(proxy)
            } else {
                awaitProxiesInModule.remove(type)
                module.provideComponent(proxy)
            }
            if (state == RuntimeState.CONTAINER_INIT_COMPLETE) createContainer()
            if (state == RuntimeState.MODULE_CREATE_AWAIT) createModule()
        } catch (e: Exception) {
            e.printStackTrace()
            onFailure()
        }
    }

}

@Constructor
fun Runtime(init: RuntimeBuilder.() -> Unit = {}): RuntimeBuilder = RuntimeBuilder().apply(init)

@Constructor
fun DefaultRuntime(artifact: Dependency = DefaultArtifact(), onBuild: ModuleBlock = {}): RuntimeBuilder = Runtime {
    injectContainer(artifact, "featurea.runtime.DefaultContainer")
    injectModule("featurea.runtime.DefaultModule")
    build(onBuild)
}
