package featurea.texture.reader

import featurea.content.ResourceNotFoundException
import featurea.runtime.Container
import featurea.text.TextContent
import featurea.splitLines

class TexturePackReader(container: Container) {

    private val textContent = container.import<TextContent>()
    private val textureAtlasCache = container.import<TextureAtlasCache>()
    private val textureBuilder = TextureBuilder()
    private var isInit: Boolean = false

    internal suspend fun init(block: () -> Unit) {
        if (isInit) return
        val source = textContent.findTextOrNull(TEXTURES_PACK_PATH) ?: throw ResourceNotFoundException(TEXTURES_PACK_PATH)
        val tokens = source.splitLines(2)
        for (token in tokens) {
            if (token.isBlank()) continue
            val lines = token.splitLines()
            // 1. atlas
            val atlas = run {
                val atlasName = lines[0]
                val texturePackDir = TEXTURES_PACK_DIRECTORY_PATH
                val atlasPath = "$texturePackDir/$atlasName"
                val sizeTokens = lines[1].trim().replace("size: ", "").split(",")
                val width = sizeTokens[0].trim().toFloat()
                val height = sizeTokens[1].trim().toFloat()
                TextureAtlas(atlasPath).apply { size.assign(width, height) }
            }
            textureBuilder.atlas = atlas

            // 2. textures
            for (index in 5 until lines.size) {
                val line = lines[index]
                if (!line.startsWith(" ")) {
                    textureBuilder.path = line
                } else if (line.startsWith("  xy: ")) {
                    val positionTokens = line.replace("  xy: ", "").split(",")
                    textureBuilder.x = positionTokens[0].trim().toFloat()
                    textureBuilder.y = positionTokens[1].trim().toFloat()
                } else if (line.startsWith("  size: ")) {
                    val sizeTokens = line.replace("  size: ", "").split(",")
                    textureBuilder.w = sizeTokens[0].trim().toFloat()
                    textureBuilder.h = sizeTokens[1].trim().toFloat()
                }
                if (textureBuilder.hasTexture()) {
                    atlas.textureRegions[textureBuilder.path!!] = textureBuilder.buildTextureRegion()
                    textureBuilder.clear()
                }
            }
            textureAtlasCache.textureAtlasMap[atlas.path] = atlas
        }
        block()
        isInit = true
    }

}

/*internals*/

private class TextureBuilder {

    lateinit var atlas: TextureAtlas
    var path: String? = null
    var x = -1f
    var y = -1f
    var w = -1f
    var h = -1f

    fun clear() {
        path = null
        x = -1f
        y = -1f
        w = -1f
        h = -1f
    }

    fun hasTexture() = path != null && x != -1f && y != -1f && w != -1f && h != -1f

    fun buildTextureRegion(): TextureRegion {
        val path = path ?: error("path not defined")
        val x1 = x
        val y1 = y
        val x2 = x + w
        val y2 = y + h
        val u1 = x1 / atlas.size.width
        val v1 = (atlas.size.height - y2) / atlas.size.height
        val u2 = x2 / atlas.size.width
        val v2 = (atlas.size.height - y1) / atlas.size.height
        return TextureRegion(atlas, path, x1, y1, x2, y2, u1, v1, u2, v2)
    }

}
