package featurea.runtime

import kotlin.reflect.KProperty

class PropertyRegistry<T> {

    private val currentValues = mutableMapOf<KProperty<*>, Any?>()
    private val initialValues = mutableMapOf<Property, Any?>()
    private val listenerMap = mutableMapOf<Property, MutableList<() -> Unit>>()

    fun initProperty(receiver: Any, reference: KProperty<*>, initialValue: Any?) {
        val property = Property(receiver, reference.name)
        initialValues[property] = initialValue
    }

    fun registerListener(receiver: Any, reference: KProperty<*>, listener: () -> Unit) {
        val property = Property(receiver, reference.name)
        val listeners = listenerMap.getOrPut(property) { mutableListOf() }
        listeners.add(listener)
    }

    operator fun getValue(receiver: Any, reference: KProperty<*>): T {
        if (!currentValues.containsKey(reference)) {
            val property = Property(receiver, reference.name)
            currentValues[reference] = initialValues[property] as T
        }
        return currentValues[reference] as T
    }

    operator fun setValue(receiver: Any, reference: KProperty<*>, value: T) {
        currentValues[reference] = value
        updateListeners(receiver, reference)
    }

    /*internals*/

    internal fun updateListeners(receiver: Any, reference: KProperty<*>) {
        val property = Property(receiver, reference.name)
        val listeners = listenerMap[property] ?: return
        for (listener in listeners) {
            try {
                listener()
            } catch (e: Throwable) {
                e.printStackTrace() // quickfix todo improve
            }
        }
    }

}
