package featurea.script

import featurea.Stack

class StackFrame {

    val args: Stack<Any?>
    val localVariables: MutableMap<String, Any?>
    var result: Any? = null

    constructor(args: Stack<Any?>) {
        this.args = args
        this.localVariables = mutableMapOf()
    }

    constructor(localVariables: MutableMap<String, Any?>) {
        this.args = Stack()
        this.localVariables = localVariables
    }

}

val StackFrame.hasArguments: Boolean get() = args.isNotEmpty()

val StackFrame.hasResult: Boolean get() = result != null
