package featurea.color

import kotlin.native.concurrent.ThreadLocal

@ThreadLocal
object ColorCache {

    private val colors: MutableMap<Long, Color> = mutableMapOf()

    fun getColor(rgba: Long): Color {
        val color = colors[rgba]
        return if (color != null) {
            color
        } else {
            val red = (rgba shr 24 and 0xFF).toFloat() / 255f
            val green = (rgba shr 16 and 0xFF).toFloat() / 255f
            val blue = (rgba shr 8 and 0xFF).toFloat() / 255f
            val alpha = (rgba and 0xFF).toFloat() / 255f
            val newColor = Color(red, green, blue, alpha)
            colors[rgba] = newColor
            newColor
        }
    }

    fun clear() = colors.clear()

}

fun String.toColor(): Color = removePrefix("#").toLong(16).toColor()

fun Number.toColor(): Color = ColorCache.getColor(toLong())
