package featurea.script

import featurea.isDouble
import featurea.isInteger
import featurea.isWrapped

internal class ScriptResult {

    var value: Any? = null

    fun assignPrimitiveValue(primitiveValue: String): Boolean {
        if (primitiveValue == "null") {
            value = null
        } else if (primitiveValue == "false") {
            value = false
        } else if (primitiveValue == "true") {
            value = true
        } else if (primitiveValue.isWrapped("'")) {
            value = primitiveValue.substring(1, primitiveValue.lastIndex)
        } else if (primitiveValue.isInteger()) {
            value = primitiveValue.toInt()
        } else if (primitiveValue.isDouble()) {
            value = primitiveValue.toDouble()
        } else {
            return false
        }
        return true
    }

}
