package featurea.text.reader

import featurea.System
import featurea.log
import featurea.normalizedPath
import org.w3c.xhr.XMLHttpRequest
import kotlin.coroutines.suspendCoroutine

actual suspend fun System.readTextFileOrNull(filePath: String, limit: Int): String? {
    for (contentRoot in contentRoots) {
        val text = loadHttpResourceFromServer(contentRoot, filePath, limit)
        if (text != null) {
            return text
        }
    }
    return null
}

// todo make use of `limit`
private suspend fun System.loadHttpResourceFromServer(contentRoot: String, filePath: String, limit: Int): String? {
    val textPath = if (filePath.startsWith("\\")) {
        filePath.removePrefix("\\")
    } else {
        "${workingDir ?: "bundle"}/${filePath.removePrefix("/")}"
    }.normalizedPath
    log("[TextReader] getText: $filePath")

    return suspendCoroutine { continuation ->
        try {
            val request = XMLHttpRequest()
            request.open("GET", textPath, true)
            request.onload = {
                if (request.readyState == 4.toShort() && request.status == 200.toShort()) {
                    continuation.resumeWith(Result.success(request.responseText))
                } else {
                    continuation.resumeWith(Result.success(null))
                }
            }
            request.onerror = {
                continuation.resumeWith(Result.success(null))
            }
            request.send(null)
        } catch (e: Throwable) {
            e.printStackTrace()
            continuation.resumeWith(Result.success(null))
        }
    }
}
