package learnopengl

import featurea.log
import featurea.math.Matrix
import featurea.math.Vector
import featurea.math.aspectRatio
import featurea.opengl.*
import featurea.runtime.Module
import featurea.shader.ShaderSource
import featurea.shader.offsetOf
import featurea.shader.readShaderSource
import featurea.shader.stride
import learnopengl.Resources.test3dShader

fun CoordinateSystemsTest(module: Module) = object : Test(module) {

    private val model = Matrix()
    private val view = Matrix()
    private val projection = Matrix()
    private lateinit var program: GLProgram
    private lateinit var vbo: GLArrayBuffer
    private lateinit var shaderSource: ShaderSource

    override fun resize() {
        projection.assignPerspective(near, far, fov, graphics.surface.size.aspectRatio)
    }

    override suspend fun load() {
        // vertices
        vbo = gl.createBuffer() ?: return
        gl.bindBuffer(ARRAY_BUFFER, vbo)
        gl.bufferData(ARRAY_BUFFER, cubeVerticesWithUvs, STATIC_DRAW)

        // shader
        shaderSource = textContent.readShaderSource(test3dShader)

        val vertexShader = gl.createShader(VERTEX_SHADER) ?: return
        gl.shaderSource(vertexShader, shaderSource.vs)
        gl.compileShader(vertexShader)
        if (gl.getShaderiv(vertexShader, COMPILE_STATUS) != TRUE) log(gl.getShaderInfoLog(vertexShader))

        val fragmentShader = gl.createShader(FRAGMENT_SHADER) ?: return
        gl.shaderSource(fragmentShader, shaderSource.ps)
        gl.compileShader(fragmentShader)
        if (gl.getShaderiv(fragmentShader, COMPILE_STATUS) != TRUE) log(gl.getShaderInfoLog(fragmentShader))

        program = gl.createProgram() ?: return
        gl.attachShader(program, vertexShader)
        gl.attachShader(program, fragmentShader)
        gl.linkProgram(program)
        if (gl.getProgramiv(program, LINK_STATUS) != TRUE) log(gl.getProgramInfoLog(program))

        for ((index, attribute) in shaderSource.attributes.withIndex()) {
            attribute.location = gl.getAttribLocation(program, attribute.name) ?: continue
            attribute.offset = shaderSource.attributes.offsetOf(index)
        }

        gl.deleteShader(vertexShader)
        gl.deleteShader(fragmentShader)
    }

    override fun update(elapsedTime: Float) {
        gl.clearColor(0.2f, 0.3f, 0.3f, 1.0f)
        gl.enable(DEPTH_TEST)
        gl.clear(COLOR_BUFFER_BIT or DEPTH_BUFFER_BIT)

        gl.useProgram(program)

        val texture1 = diffuseTexture
        if (texture1 != null) {
            val texture = textureProvider.findTextureOrNull(texture1)?.id

            // glsl: texture -> slot 0
            gl.uniform1i(gl.getUniformLocation(program, "texture"), 0) // texture -> slot 0
            // kotlin: texture -> slot 0
            gl.activeTexture(TEXTURE0) // -> slot 0
            gl.bindTexture(TEXTURE_2D, texture) // texture ->

            gl.texParameteri(TEXTURE_2D, TEXTURE_WRAP_S, textureWrap.s)
            gl.texParameteri(TEXTURE_2D, TEXTURE_WRAP_T, textureWrap.t)
            gl.texParameteri(TEXTURE_2D, TEXTURE_MIN_FILTER, LINEAR)
            gl.texParameteri(TEXTURE_2D, TEXTURE_MAG_FILTER, LINEAR)
        }

        rotationAngle += rotationIncrement * elapsedTime / 1_000f
        model.assignIdentity().rotate(rotationAxis, rotationAngle)
        view.assignTranslation(viewTranslation)
        gl.uniformMatrix4fv(gl.getUniformLocation(program, "model"), false, model)
        gl.uniformMatrix4fv(gl.getUniformLocation(program, "view"), false, view)
        gl.uniformMatrix4fv(gl.getUniformLocation(program, "projection"), false, projection)

        gl.bindBuffer(ARRAY_BUFFER, vbo)
        for (attr in shaderSource.attributes) {
            gl.enableVertexAttribArray(attr.location)
            gl.vertexAttribPointer(attr.location, attr.size, FLOAT, false, shaderSource.attributes.stride, attr.offset)
        }

        gl.drawArrays(TRIANGLES, 0, 6)

        gl.bindTexture(TEXTURE_2D, null)
        gl.activeTexture(TEXTURE1) // disable texture slot
        gl.bindBuffer(ELEMENT_ARRAY_BUFFER, null)
        gl.bindBuffer(ARRAY_BUFFER, null)
    }

}

fun testCoordinateSystems(module: Module): Test = CoordinateSystemsTest(module).apply {
    diffuseTexture = "images/container.png"
    textureWrap.s = CLAMP_TO_EDGE
    textureWrap.t = CLAMP_TO_EDGE
    rotationAngle = 0.0
    rotationAxis = Vector(1.5f, 4.5f, -10f)
    rotationIncrement = 2f
    viewTranslation = Vector(0f, 0f, -2.5f)
    near = 0.1f
    far = 100f
    fov = 45f
}
