package featurea.config

import featurea.content.loaders
import featurea.rml.RmlContent
import featurea.runtime.Artifact

/*dependencies*/

val artifact = Artifact("featurea.config") {
    "ConfigContent" to ConfigContent::class
    "ConfigReader" to ConfigReader::class

    loaders {
        "ConfigLoader" to ::ConfigLoader
    }

    static {
        val rmlContent = import<RmlContent>()
        provideComponent(ConfigContent(rmlContent))
        provideComponent(ConfigReader(container = this))
    }
}
