package learnopengl.types

import featurea.opengl.*

class TextureFilter {
    var minFilter: Int = NEAREST_MIPMAP_LINEAR
    var magFilter: Int = LINEAR
}

fun textureMinFilterOf(value: String): Int = when (value) {
    "NEAREST" -> NEAREST
    "LINEAR" -> LINEAR
    "NEAREST_MIPMAP_NEAREST" -> NEAREST_MIPMAP_NEAREST
    "NEAREST_MIPMAP_LINEAR" -> NEAREST_MIPMAP_LINEAR
    "LINEAR_MIPMAP_NEAREST" -> LINEAR_MIPMAP_NEAREST
    "LINEAR_MIPMAP_LINEAR" -> LINEAR_MIPMAP_LINEAR
    else -> error("value: $value")
}

fun textureMagFilterOf(value: String): Int = when (value) {
    "NEAREST" -> NEAREST
    "LINEAR" -> LINEAR
    else -> error("value: $value")
}
