package learnopengl.types

import featurea.opengl.CLAMP_TO_BORDER
import featurea.opengl.CLAMP_TO_EDGE
import featurea.opengl.MIRRORED_REPEAT
import featurea.opengl.REPEAT

class TextureWrap {
    var s: Int = REPEAT
    var t: Int = REPEAT
}

fun textureWrapOf(value: String): Int = when (value) {
    "CLAMP_TO_EDGE" -> CLAMP_TO_EDGE
    "CLAMP_TO_BORDER" -> CLAMP_TO_BORDER
    "MIRRORED_REPEAT" -> MIRRORED_REPEAT
    "REPEAT" -> REPEAT
    "MIRROR_CLAMP_TO_EDGE" -> REPEAT
    else -> error("value: $value")
}
