package learnopengl

import featurea.runOnApplicationThread
import featurea.runtime.Component
import featurea.runtime.injectDependencies
import featurea.Application

import featurea.loader.loadResources
import featurea.window.WindowRuntime

fun runOnMainLoop(test: Component.() -> Test) = injectDependencies(artifact) {
    WindowRuntime {
        val app = module.importComponent<Application>()

        runOnApplicationThread {
            loadResources(requiredResources) {
                val test = app.test()
                test.diffuseTexture?.also { loadResources(it) }
                test.specularTexture?.also { loadResources(it) }
                test.emissionTexture?.also { loadResources(it) }
                test.load()
                app.delegate = test
            }
        }
    }

}
