package featurea.runtime

class ContainerRegistry internal constructor() {

    private val containers = ComponentRegistry<Container>()

    operator fun get(key: String): Container? = containers[key]

    fun injectContainer(canonicalName: String, container: Container) {
        check(!containers.containsKey(canonicalName))
        check(!containers.contains(container))
        container.registry = this
        container.key = canonicalName
        containers.inject(container.key, container)
    }

    fun appendContainer(canonicalName: String, container: Container) {
        check(!containers.contains(container))
        container.registry = this
        containers.append(canonicalName, container)
    }

    fun removeContainer(container: Container) = removeContainer(container.key)

    fun removeContainer(key: String) = containers.remove(key)?.onDestroy()

    fun findContainer(canonicalName: String): Container =
        containers[canonicalName] ?: throw ContainerNotFoundException(canonicalName)

    fun destroy() {
        for (container in containers) container.destroy()
    }

}
