package featurea.content

import featurea.runtime.*

class ResourceLoaderRegistry(override val module: Module) : Component {

    private val resourceLoaderMap = linkedMapOf<String, ResourceLoader>()

    fun registerResourceLoader(contentType: String, resourceLoader: ResourceLoader) {
        resourceLoaderMap[contentType] = resourceLoader
    }

    fun findResourcesOrNull(resourceTag: ResourceTag, key: String, value: String): List<String>? {
        for ((contentType, resourceLoader) in resourceLoaderMap) {
            val resources = resourceLoader.findResourcesOrNull(resourceTag, key, value)
            if (resources != null) {
                return resources
            }
        }
        return null
    }

    suspend fun loadingResource(resource: Resource) {
        val resourceLoader = findResourceLoader(resource.contentType)
        resourceLoader.loadingResource(resource)
    }

    suspend fun releaseResource(resource: Resource) {
        val resourceLoader = findResourceLoader(resource.contentType)
        resourceLoader.releaseResource(resource)
    }

    /*internals*/

    private fun findResourceLoader(contentType: String): ResourceLoader =
        resourceLoaderMap[contentType] ?: throw ResourceLoaderNotFoundException(contentType)

}
