package featurea.texture

import featurea.*
import featurea.content.*
import featurea.opengl.GLES20
import featurea.opengl.TEXTURE_2D
import featurea.runtime.Component
import featurea.runtime.Module
import featurea.runtime.import
import featurea.texture.reader.*

class TextureLoader(module: Module) : ResourceLoader(module, TEXTURE_CONTENT_TYPE) {

    private val textureAtlasCache = import<TextureAtlasCache>()
    private val textureCache = mutableMapOf<String, Texture>()
    private val system = import<System>()
    private val gl = import<GLES20>()

    override fun findResourcesOrNull(resourceTag: ResourceTag, key: String, value: String): List<String>? = null

    override suspend fun loadingResource(resource: Resource) {
        if (resource.path.extension == GIF) {
            if (system.useTexturePack) {
                val resourceDir = "$GIF_CACHE_PATH/${resource.path.normalizedPath.removePrefix("/")}"
                val textureRegionPaths = mutableListOf<String>()
                for (index in 0 until resource.manifest.frameCount) {
                    textureRegionPaths.add("${resourceDir}/${index}.png")
                }
                for (textureRegionPath in textureRegionPaths) {
                    loadTextureRegion(textureRegionPath, resource.loadingQueue)
                }
            } else {
                for (file in resource.files) {
                    loadTextureRegion(file, resource.loadingQueue)
                }
            }
            resource.loadingQueue.add(resource.path)
        }
        if (resource.path.hasExtension(JPEG, JPG, PNG)) {
            if (system.useTexturePack) {
                loadTextureRegion(resource.path, resource.loadingQueue)
            } else {
                loadTextureRegion(resource.manifest.texture, resource.loadingQueue)
            }
        }
    }

    override suspend fun releaseResource(resource: Resource) {
        if (resource.path.extension == GIF) {
            for (file in resource.files) {
                releaseTextureRegion(file, resource.releaseQueue)
            }
        }
        if (resource.path.hasExtension(JPEG, JPG, PNG)) {
            releaseTextureRegion(resource.manifest.texture, resource.releaseQueue)
        }
    }

    suspend fun loadTextureRegion(texturePath: String, loadedResourcePaths: MutableList<String>) {
        val textureAtlas = textureAtlasCache.getTextureAtlas(texturePath)
        if (textureCache[textureAtlas.path] == null) {
            val id = gl.createTexture(texturePath) ?: error("texturePath: $texturePath")
            val texture = Texture(textureAtlas, id)
            gl.enableTexture(texture)
            loadTexture(textureAtlas)
            check(textureAtlas.size.isNotEmpty()) { "Texture not loaded: $texturePath" }
            textureAtlasCache.cacheTextureAtlas(textureAtlas)
            gl.generateMipmap(TEXTURE_2D)
            gl.disableTexture()
            textureCache[textureAtlas.path] = texture
        }
        loadedResourcePaths.addAll(textureAtlas.textureRegionPaths)
    }

    fun releaseTextureRegion(textureRegionPath: String, releasedResourcePaths: MutableList<String>) {
        val textureAtlas = textureAtlasCache.textureAtlasMap[textureRegionPath]
        if (textureAtlas == null) {
            releasedResourcePaths.add(textureRegionPath)
            return
        }
        releasedResourcePaths.addAll(textureAtlas.textureRegionPaths)
        val texture = textureCache.remove(textureAtlas.path)
        if (texture != null) {
            textureAtlasCache.removeTextureAtlas(texture.atlas.path)
            gl.deleteTexture(texture.id)
        }
    }

    fun findTextureOrNull(textureRegionPath: String): Texture? {
        val textureAtlas = textureAtlasCache.getTextureAtlas(textureRegionPath)
        val texture = textureCache[textureAtlas.path]
        return texture
    }

}

expect suspend fun Component.loadTexture(textureAtlas: TextureAtlas)
