package learnopengl

import featurea.log
import featurea.opengl.*
import featurea.runtime.Module
import featurea.shader.readShaderSource
import learnopengl.Resources.imageShader

fun BlendTest(module: Module) = object : Test(module) {

    var vertices = floatArrayOf(
        // positions        // texture coordinates
        +0.9f, +0.9f, 0.0f, 1.0f, 1.0f, // top right
        +0.9f, -0.9f, 0.0f, 1.0f, 0.0f, // bottom right
        -0.9f, -0.9f, 0.0f, 0.0f, 0.0f, // bottom left

        +0.9f, +0.9f, 0.0f, 1.0f, 1.0f, // top right
        -0.9f, -0.9f, 0.0f, 0.0f, 0.0f, // bottom left
        -0.9f, +0.9f, 0.0f, 0.0f, 1.0f, // top left
    )
    lateinit var shaderProgram: GLProgram
    lateinit var vbo: GLArrayBuffer

    init {
        gl.clearColor(0f, 0f, 0f, 1f)
    }

    override suspend fun load() {
        val shaderSource = textContent.readShaderSource(imageShader)

        vbo = gl.createBuffer() ?: return
        gl.bindBuffer(ARRAY_BUFFER, vbo)
        gl.bufferData(ARRAY_BUFFER, vertices, STATIC_DRAW)

        val vertexShader = gl.createShader(VERTEX_SHADER) ?: return
        gl.shaderSource(vertexShader, shaderSource.vs)
        gl.compileShader(vertexShader)
        if (gl.getShaderiv(vertexShader, COMPILE_STATUS) != TRUE) log(gl.getShaderInfoLog(vertexShader))

        val fragmentShader = gl.createShader(FRAGMENT_SHADER) ?: return
        gl.shaderSource(fragmentShader, shaderSource.ps)
        gl.compileShader(fragmentShader)
        if (gl.getShaderiv(fragmentShader, COMPILE_STATUS) != TRUE) log(gl.getShaderInfoLog(fragmentShader))

        shaderProgram = gl.createProgram() ?: return
        gl.attachShader(shaderProgram, vertexShader)
        gl.attachShader(shaderProgram, fragmentShader)
        gl.linkProgram(shaderProgram)
        if (gl.getProgramiv(shaderProgram, LINK_STATUS) != TRUE) log(gl.getProgramInfoLog(shaderProgram))

        gl.deleteShader(vertexShader)
        gl.deleteShader(fragmentShader)
    }

    override fun update(elapsedTime: Float) {
        gl.clear(COLOR_BUFFER_BIT)

        val (r, g, b, a) = blendFunc.color
        gl.blendColor(r, g, b, a)
        gl.blendFunc(blendFunc.sfactor, blendFunc.dfactor)

        gl.useProgram(shaderProgram)

        val texture1 = diffuseTexture
        if (texture1 != null) {
            val texture = textureProvider.findTextureOrNull(texture1)?.id
            gl.activeTexture(TEXTURE0)
            gl.bindTexture(TEXTURE_2D, texture)
            gl.texParameteri(TEXTURE_2D, TEXTURE_WRAP_S, textureWrap.s)
            gl.texParameteri(TEXTURE_2D, TEXTURE_WRAP_T, textureWrap.t)
            gl.texParameteri(TEXTURE_2D, TEXTURE_MIN_FILTER, textureFilter.minFilter)
            gl.texParameteri(TEXTURE_2D, TEXTURE_MAG_FILTER, textureFilter.magFilter)
            gl.uniform1i(gl.getUniformLocation(shaderProgram, "ourTexture"), 0)
        }

        gl.uniform1f(gl.getUniformLocation(shaderProgram, "alphaTest"), alphaTest)
        gl.uniform1f(gl.getUniformLocation(shaderProgram, "alphaTest2"), alphaTest2)

        gl.bindBuffer(ARRAY_BUFFER, vbo)

        gl.enableVertexAttribArray(0)
        gl.vertexAttribPointer(0, 3, FLOAT, false, 5 * Float.SIZE_BYTES, 0 * Float.SIZE_BYTES)

        gl.enableVertexAttribArray(1)
        gl.vertexAttribPointer(1, 2, FLOAT, false, 5 * Float.SIZE_BYTES, 3 * Float.SIZE_BYTES)

        gl.drawArrays(TRIANGLES, 0, 6)
    }

}
