package featurea.content

import featurea.breakpoint

class ResourceTag(val name: String, parent: ResourceTag? = null, filePath: String? = parent?.filePath) {

    var parent: ResourceTag? = parent
        internal set
    var filePath: String? = filePath
        internal set
    val properties = mutableMapOf<String, ResourceTag>()
    val attributes = mutableMapOf<String, String>()
    val children = mutableListOf<ResourceTag>()

    init {
        if (name == "Form") {
            breakpoint()
        }
    }

    val id: String? get() = attributes["id"]
    val isRoot: Boolean get() = parent == null
    val rootRmlTag: ResourceTag get() = parent?.rootRmlTag ?: this

    fun indexOf(child: ResourceTag) = children.indexOf(child)

    override fun toString() = """
    |name: $name
    |properties: $properties
    |children: $children
    """.trimMargin("|")

}

fun ResourceTag.appendChildResourceTag(child: ResourceTag) {
    require(!children.contains(child))
    children.add(child)

    // >> todo make this consistent
    val childId = child.id
    if (childId != null) {
        properties[childId] = child
    }
    // <<

    // >> todo replace it to `assignChild` method by fixing RmlFileParser
    require(child.parent == null)
    child.parent = this
    // <<
}

fun ResourceTag.removeChildResourceTag(child: ResourceTag) {
    require(children.contains(child))
    children.remove(child)
    require(child.parent != null)
    child.parent = null
}