package featurea.loader

import featurea.*
import featurea.content.*
import featurea.rml.RmlContent
import featurea.rml.RmlResource
import featurea.rml.buildApplication
import featurea.rml.forEachResourceAttribute
import featurea.runtime.Component
import featurea.runtime.ComponentTask
import featurea.runtime.Module
import featurea.runtime.import
import featurea.text.TextContent

class Loader(module: Module) : ApplicationController(module) {

    private val app = import<Application>()
    private val content = import<Content>()
    private val resourceLoaderRegistry = import<ResourceLoaderRegistry>()
    var isLoading = false
        private set

    val listeners = mutableListOf<LoaderListener>()

    internal var loadingPool = mutableListOf<String>()
    internal var releasePool = mutableListOf<String>()
    private var capacity: Float = 0f
    private val progress: Float get() = if (capacity == 0f) 0f else (capacity - loadingPool.size) / capacity

    internal val loadingResources = mutableSetOf<String>()

    fun isResourceLoaded(resource: String): Boolean {
        return loadingResources.contains(resource)
    }

    override suspend fun update() {
        if (!isLoading && loadingPool.isNotEmpty()) {
            activate()
        }

        if (isLoading) {
            // 1. clear all releasePool
            if (releasePool.isNotEmpty()) {
                for (resourcePath in releasePool) {
                    val existingResource = content.existingResources[resourcePath]
                    if (existingResource != null) {
                        resourceLoaderRegistry.releaseResource(existingResource)
                        existingResource.releaseQueue.distinct().forEach { releasePool.remove(it) }
                        existingResource.releaseQueue.clear()
                    } else {
                        releasePool.remove(resourcePath) // just for now todo delete this
                    }
                }
            }

            // 2. load next resource from loadingPool
            if (loadingPool.isNotEmpty()) {
                val resourcePath = loadingPool[0]
                try {
                    val resource = content.findResourceOrNull(resourcePath)
                    if (resource != null) {
                        resourceLoaderRegistry.loadingResource(resource)
                        resource.loadingQueue.distinct().forEach { loadingPool.remove(it) }
                        resource.loadingQueue.clear()
                    }
                } finally {
                    loadingPool.remove(resourcePath) // IMPORTANT because `palette-desktop.properties`
                }
            }

            // 3. inform ui that loading is progressed
            updateProgress(progress)
        }

        // >> quickfix todo improve
        if (isLoading) {
            app.delegate?.loading(progress)
        }
        // <<
    }

    /*internals*/

    private fun activate() {
        isLoading = true
        capacity = loadingPool.size.toFloat()
    }

    private fun complete() {
        isLoading = false
        capacity = 0f
    }

    private suspend fun updateProgress(progress: Float) {
        for (listener in listeners) {
            listener.updateLoading(progress)
        }
        if (progress == 1f) {
            // 1. listeners
            complete()
            for (listener in listeners) {
                listener.completeLoading()
            }

            // 2. repeat tasks
            for (task in app.tasksToRepeatOnCompleteLoadingApplication) {
                task()
            }

            // 3. run tasks
            val tasks = app.tasksToRunOnCompleteLoading.swap()
            for (task in tasks) {
                task()
            }
            tasks.clear()
        }
    }

}

/*convenience*/

fun Component.loadResources(vararg resourcePaths: String, complete: ComponentTask? = null) {
    loadResources(resourcePaths.asIterable(), complete)
}

fun Component.loadResources(resourcePaths: Iterable<String>, complete: ComponentTask? = null) {
    val app = import<Application>()
    val appLoader = import<Loader>()

    val resourcePaths = resourcePaths.distinct().filter { resource -> !appLoader.isResourceLoaded(resource) }
    if (resourcePaths.isNotEmpty()) {
        appLoader.loadingPool.addAll(resourcePaths)
        appLoader.loadingPool = appLoader.loadingPool.distinct().toMutableList()
        appLoader.loadingResources.addAll(resourcePaths)
    }
    if (complete != null) {
        app.tasksToRunOnCompleteLoading.add(complete)
    }
}

suspend fun Component.loadResources(rmlTag: ResourceTag, key: String, value: String, complete: ComponentTask = {}) {
    val content = import<Content>()
    val resourceLoaderRegistry = import<ResourceLoaderRegistry>()

    val resourcePaths = resourceLoaderRegistry.findResourcesOrNull(rmlTag, key, value)
    if (resourcePaths != null) {
        loadResources(resourcePaths, complete)
    } else {
        // >> quickfix todo improve
        if (content.findResourceOrNull(value) != null) {
            loadResources(listOf(value), complete)
        }
        // <<
    }
}

fun Component.releaseResources(vararg resourcePaths: String) {
    releaseResources(resourcePaths.asIterable())
}

fun Component.releaseResources(resourcePaths: Iterable<String>) {
    val appLoader = import<Loader>()

    val resourcePaths = resourcePaths.distinct().filter { resource -> appLoader.isResourceLoaded(resource) }
    appLoader.releasePool.addAll(resourcePaths)
    appLoader.releasePool = appLoader.releasePool.distinct().toMutableList()
}

suspend fun Component.loadBundle(bundlePath: String, init: ComponentTask = {}) {
    val content = import<Content>()
    val system = import<System>()

    initContentAndSystemWithBundle(bundlePath)
    val mainProject = system.properties.mainProject
    val mainDocument = system.properties.mainDocument
    loadResources(content.requiredResources, "$mainProject:/$mainDocument", init)
}

suspend fun Component.loadResource(resource: String, init: suspend () -> Unit = {}) {
    val rmlContent = import<RmlContent>()

    val rmlResource: RmlResource = rmlContent.findRmlResource(resource)
    return loadResource(rmlResource, init)
}

suspend fun Component.loadResource(rmlResource: RmlResource, init: suspend () -> Unit = {}) {
    val content = import<Content>()

    rmlResource.rmlTag.forEachResourceAttribute { rmlTag, key, value ->
        loadResources(rmlTag, key, value)
    }
    loadResources(content.requiredResources) {
        init()
    }
}

/*internals*/

private suspend fun Component.loadResources(
    resourcePaths: Iterable<String>,
    screenPath: String,
    init: ComponentTask = {}
) {
    val app = import<Application>()
    val appLoader = import<Loader>()

    loadApplicationResources(resourcePaths) {
        app.delegate = module.buildApplication(screenPath)
        init()
        if (app.delegate != null) {
            for (listener in appLoader.listeners) {
                listener.completeLoadingApplication()
            }
        }
    }
}

private suspend fun Component.loadApplicationResources(resourcePaths: Iterable<String>, complete: ComponentTask) {
    val app = import<Application>()
    val appLoader = import<Loader>()

    for (listener in appLoader.listeners) {
        listener.startLoading()
    }
    for (task in app.tasksToRepeatOnStartLoading) {
        task()
    }
    loadResources(resourcePaths) {
        complete()
    }
}

private suspend fun Component.initContentAndSystemWithBundle(bundlePath: String) {
    val content = import<Content>()
    val system = import<System>()
    val textContent = import<TextContent>()
    check(content.requiredResources.isEmpty())

    system.workingDir = bundlePath
    system.contentRoots.add(0, bundlePath)
    val text = textContent.findTextOrNull("manifest.properties")
    checkNotNull(text)

    val bundleManifest = parseProperties(text)
    val bundleResources = bundleManifest["resources"]?.splitWithWrappers(',')
    checkNotNull(bundleResources)

    system.properties.entries.putAll(bundleManifest)
    content.requiredResources.addAll(bundleResources)
}
