package featurea

class Properties() {

    val entries = mutableMapOf<String, Any?>()

    constructor(init: Properties.() -> Unit) : this() {
        init()
    }

    constructor(vararg original: Pair<String, Any?>) : this() {
        for ((key, value) in original) {
            entries[key] = value
        }
    }

    inline operator fun <reified T : Any?> get(key: String): T? {
        val value = entries[key] ?: return null
        return when (T::class) {
            List::class -> {
                if (value is List<*>) return value as T
                else value.toString().split(", ")
            }
            Boolean::class -> value.toString().toBoolean()
            Byte::class -> value.toString().toByte()
            Int::class -> value.toString().toInt()
            Long::class -> value.toString().toLong()
            Float::class -> value.toString().toFloat()
            Double::class -> value.toString().toDouble()
            String::class -> value
            else -> error("key: $key")
        } as T
    }

    operator fun set(key: String, value: Any?) {
        entries[key] = value
    }

    fun remove(key: String): Any? {
        return entries.remove(key)
    }

}
