package featurea.rml

import featurea.content.RmlSchema
import featurea.text.TextContent
import featurea.runtime.Component
import featurea.runtime.import
import featurea.toConfigPath
import featurea.toFilePath
import featurea.toIdPath
import featurea.parseProperties
import featurea.text.reader.readTextFileOrNull

class RmlContent(val textContent: TextContent, val configPackages: List<String>) {

    private val rmlFiles = mutableMapOf<String, RmlFile>()
    private val rmlSchemas = mutableMapOf<String, RmlSchema>()

    suspend fun findRmlSchema(resourcePackage: String): RmlSchema {
        var rmlSchema = rmlSchemas[resourcePackage]
        if (rmlSchema == null) {
            rmlSchema = RmlSchema().apply {
                clearCaches()
                for (configPackage in configPackages) {
                    val configPath = configPackage.toConfigPath()
                    val text = textContent.findTextOrNull("${configPath}/package.properties")
                    if (text != null) {
                        val properties = parseProperties(text)
                        appendProperties(properties)
                    }
                }
            }
            rmlSchemas[resourcePackage] = rmlSchema
        }
        return rmlSchema
    }

    suspend fun findRmlFile(filePath: String, source: String? = null): RmlFile {
        var rmlFile = rmlFiles[filePath]
        if (rmlFile == null) {
            val text = source ?: (textContent.findTextOrNull(filePath) ?: throw RmlResourceNotFoundException(filePath))
            rmlFile = RmlFile().apply { init(text, filePath) { findRmlSchema(packageId) } }
            rmlFiles[filePath] = rmlFile
        }
        return rmlFile
    }

    suspend fun readRmlFile(filePath: String): RmlFile? {
        val text = textContent.system.readTextFileOrNull(filePath) ?: return null
        return RmlFile().apply { init(text, filePath) { findRmlSchema(packageId) } }
    }

    suspend fun findRmlResource(resourcePath: String, source: String? = null): RmlResource {
        val filePath = resourcePath.toFilePath()
        val idPath = resourcePath.toIdPath()
        val rmlFile = findRmlFile(filePath, source)
        val property = rmlFile.rmlTag.getPropertyByIdPath(idPath)
        checkNotNull(property)
        return RmlResource().apply { rmlTag = property; this.rmlFile = rmlFile }
    }

    fun removeCachedRmlFile(filePath: String) {
        rmlFiles.remove(filePath)
    }

    fun clear() {
        rmlFiles.clear()
        rmlSchemas.clear()
    }

}

suspend fun Component.readResource(resourcePath: String, source: String? = null): RmlResource {
    val rmlContent = import<RmlContent>()
    return rmlContent.findRmlResource(resourcePath, source)
}
