package featurea.content

import featurea.runtime.Container

abstract class ResourceReader(val container: Container, contentType: String) {

    init {
        val content = container.import<Content>()
        content.registerResourceReader(contentType, this)
    }

    open suspend fun instrumentResource(resourcePath: String) {}

    abstract suspend fun readResourceOrNull(resourcePath: String, isBundle: Boolean): Resource?

}
