package featurea.texture

import featurea.opengl.*
import featurea.texture.reader.TextureAtlas

class Texture(val atlas: TextureAtlas, val id: GLTexture)

fun GLES20.enableTexture(texture: Texture) {
    bindTexture(TEXTURE_2D, texture.id)
    texParameteri(TEXTURE_2D, TEXTURE_MIN_FILTER, LINEAR_MIPMAP_LINEAR)
    texParameteri(TEXTURE_2D, TEXTURE_MAG_FILTER, LINEAR)
    texParameteri(TEXTURE_2D, TEXTURE_WRAP_S, CLAMP_TO_EDGE)
    texParameteri(TEXTURE_2D, TEXTURE_WRAP_T, CLAMP_TO_EDGE)
}

fun GLES20.disableTexture() {
    bindTexture(TEXTURE_2D, null)
}
