package featurea.rml

import featurea.content.ResourceTag
import featurea.content.appendChildResourceTag
import featurea.content.removeChildResourceTag
import featurea.rml.RmlTagAccess.DIRECT
import featurea.rml.RmlTagAccess.LINK

class RmlResourceNotFoundException(resource: String) : RuntimeException(resource)

class RmlResource {

    lateinit var rmlTag: ResourceTag
    lateinit var rmlFile: RmlFile
    var builder: RmlResourceBuilder<Any, Any, Any>? = null

    internal val rmlTagByStartObject = linkedMapOf<Any, ResourceTag>() // todo make public if needed
    internal val startObjectByRmlTag = linkedMapOf<ResourceTag, Any>() // todo make public if needed
    val rmlTagByEndObject = linkedMapOf<Any, ResourceTag>() // IMPORTANT needed by editor delegate
    val endObjectByRmlTag = linkedMapOf<ResourceTag, Any>() // IMPORTANT needed by editor delegate

    suspend fun <T : Any> ResourceTag.createRmlTagEndObject(rmlTagAccess: RmlTagAccess = DIRECT): T {
        val builder = builder!!
        val rmlResource = this@RmlResource
        val rmlTag = this

        // start object
        val link = rmlTag.findLinkOrNull()
        val root = if (link == null) {
            builder.createRoot(rmlResource, rmlTag, rmlTagAccess)
        } else {
            link.createRmlTagEndObject(LINK)
        }
        setRmlTagStartObject(rmlTag, rmlTagAccess, root)

        // attributes, properties, children
        for ((key, value) in rmlTag.attributes) rmlTag.assignAttribute(key, value, root, rmlTagAccess)
        for ((key, property) in rmlTag.properties) rmlTag.assignProperty(key, property, root, rmlTagAccess)
        for (child in rmlTag.children) rmlTag.assignChild(child, root, rmlTagAccess)

        // end object
        val rmlTagEndObject = rmlTag.build(root, rmlTagAccess)
        setRmlTagEndObject(rmlTag, rmlTagAccess, rmlTagEndObject)
        return rmlTagEndObject as T
    }

    suspend fun ResourceTag.appendChild(child: ResourceTag) {
        appendChildResourceTag(child)

        // runtime
        val builder = builder
        if (builder != null) {
            val rmlResource = this@RmlResource
            val startObject: Any = startObject(rmlResource)
            val rmlTagAccess: RmlTagAccess = DIRECT
            assignChild(child, startObject, rmlTagAccess)
        }
    }

    private suspend fun ResourceTag.assignChild(child: ResourceTag, startObject: Any, rmlTagAccess: RmlTagAccess) {
        val builder = builder
        if (builder != null) {
            val rmlResource = this@RmlResource
            val rmlTag = this
            val childEndObject = child.createRmlTagEndObject<Any>(rmlTagAccess.nest())
            builder.appendChild(rmlResource, rmlTag, rmlTagAccess, startObject, child, childEndObject)
        }
    }

    suspend fun ResourceTag.insertChild(index: Int, child: ResourceTag, startObject: Any, rmlTagAccess: RmlTagAccess) {
        // data
        require(!children.contains(child))
        children.add(index, child)

        // >> todo make this consistent
        val childId = child.id
        if (childId != null) {
            properties[childId] = child
        }
        // <<

        // runtime
        val builder = builder
        if (builder != null) {
            val rmlResource = this@RmlResource
            val rmlTag = this
            val childEndObject = child.createRmlTagEndObject<Any>(rmlTagAccess.nest())
            builder.insertChild(rmlResource, rmlTag, rmlTagAccess, startObject, index, child, childEndObject)
        }
    }

    suspend fun ResourceTag.removeChild(child: ResourceTag) {
        removeChildResourceTag(child)

        // runtime
        val builder = builder
        if (builder != null) {
            val rmlResource = this@RmlResource
            val rmlTag = this
            val startObject = startObject(rmlResource)
            val endObject = child.endObject(rmlResource)
            builder.removeChild(rmlResource, rmlTag, DIRECT, startObject, child, endObject)
            rmlTagByStartObject.remove(startObjectByRmlTag.remove(child))
            rmlTagByEndObject.remove(endObjectByRmlTag.remove(child))
        }
    }

    suspend fun ResourceTag.replaceChild(index: Int, child: ResourceTag) {
        // data
        require(children.contains(child))
        children.remove(child)
        if (index >= children.size) children.add(child) else children.add(index, child)

        // runtime
        val builder = builder
        if (builder != null) {
            val rmlResource = this@RmlResource
            val rmlTag = this
            val startObject = startObject(rmlResource)
            val endObject = child.endObject(rmlResource)
            builder.replaceChild(rmlResource, rmlTag, DIRECT, startObject, child, index, endObject)
        }
    }

    suspend fun ResourceTag.assignAttribute(key: String, value: String) {
        val builder = builder
        if (builder != null) {
            val rmlResource = this@RmlResource
            val startObject: Any = startObject(rmlResource)
            val rmlTagAccess: RmlTagAccess = DIRECT
            assignAttribute(key, value, startObject, rmlTagAccess)
        } else {
            // data
            attributes[key] = value
        }
    }

    suspend fun ResourceTag.assignAttribute(key: String, value: String, startObject: Any, rmlTagAccess: RmlTagAccess) {
        // data
        attributes[key] = value

        // runtime
        val builder = builder!!
        val rmlResource = this@RmlResource
        val rmlTag = this
        builder.assignAttribute(rmlResource, rmlTag, rmlTagAccess, startObject, key, value)
    }

    suspend fun ResourceTag.removeAttribute(key: String, value: String) {
        // data
        attributes.remove(key)

        // runtime
        val builder = builder
        if (builder != null) {
            val rmlResource = this@RmlResource
            val rmlTag = this
            val startObject = startObject(rmlResource)
            builder.removeAttribute(rmlResource, rmlTag, DIRECT, startObject, key, value)
        }
    }

    suspend fun ResourceTag.assignProperty(
        key: String,
        property: ResourceTag,
        startObject: Any,
        rmlTagAccess: RmlTagAccess
    ) {
        // data
        val id = property.attributes["id"]
        require(key == id) { "key: $key, id: $id" }
        require(!properties.contains(key))
        properties[key] = property

        // runtime
        val builder = builder
        if (builder != null) {
            val rmlResource = this@RmlResource
            val rmlTag = this
            val propertyEndObject = property.createRmlTagEndObject<Any>(rmlTagAccess.nest())
            builder.assignProperty(rmlResource, rmlTag, rmlTagAccess, startObject, key, property, propertyEndObject)
        }

    }

    suspend fun ResourceTag.removeProperty(key: String, property: ResourceTag) {
        // data
        val id = property.attributes["id"]
        require(key == id) { "key: $key, id: $id" }
        properties.remove(key)

        // runtime
        val builder = builder
        if (builder != null) {
            val rmlTag = this
            val rmlResource = this@RmlResource
            val startObject = startObject(rmlResource)
            val endObject = property.endObject(rmlResource)
            builder.removeProperty(rmlResource, rmlTag, DIRECT, startObject, key, property, endObject)
        }
    }

    suspend fun ResourceTag.buildAttribute(key: String, value: String) {
        assignAttribute(key, value)
        build()
    }

    suspend fun ResourceTag.build() {
        val builder = builder
        if (builder != null) {
            val rmlResource = this@RmlResource
            val startObject = startObject(rmlResource)
            build(startObject, DIRECT)
        }
    }

    /*internals*/

    private suspend fun ResourceTag.build(startObject: Any, rmlTagAccess: RmlTagAccess): Any {
        val rmlResource = this@RmlResource
        val rmlTag = this
        val builder = builder!!
        val original = builder.createOriginal(rmlResource, rmlTag, rmlTagAccess, startObject)
        return original
    }


    private fun setRmlTagStartObject(rmlTag: ResourceTag, rmlTagAccess: RmlTagAccess, rmlTagStartObject: Any) {
        if (rmlTagAccess == DIRECT) {
            check(rmlTagByStartObject.put(rmlTagStartObject, rmlTag) == null)
            check(startObjectByRmlTag.put(rmlTag, rmlTagStartObject) == null)
        }
    }

    private fun setRmlTagEndObject(rmlTag: ResourceTag, rmlTagAccess: RmlTagAccess, rmlTagEndObject: Any) {
        if (rmlTagAccess == DIRECT) {
            check(rmlTagByEndObject.put(rmlTagEndObject, rmlTag) == null)
            check(endObjectByRmlTag.put(rmlTag, rmlTagEndObject) == null)
        }
    }

}

val RmlResource.canonicalClassName: String? get() = rmlFile.rmlSchema.canonicalClassNameByKey[rmlTag.name]

fun RmlResource.findResourceAttributeKeys(rmlTag: ResourceTag?): List<String> =
    rmlFile.rmlSchema.attributeNamesByRmlTagName[rmlTag?.name]