package featurea.graphics

import featurea.Precision
import featurea.Precision.MEDIUM
import featurea.color.Color
import featurea.color.Colors
import featurea.layout.Surface
import featurea.math.Rectangle
import featurea.nextIntMediumPrecision
import featurea.opengl.*
import featurea.prevIntMediumPrecision
import featurea.runtime.*
import kotlin.math.roundToInt

class Graphics(override val module: Module) : Component {

    val gl = import<GLES20>()
    val surface: Surface = Surface()
    val listeners = mutableListOf<GraphicsListener>()
    var precision: Precision = MEDIUM
    var useCameras: Boolean = true

    fun init() {
        // 1. listeners
        for (listener in listeners) listener.onInit()

        // 2. blend
        gl.enable(BLEND)
        gl.blendFunc(SRC_ALPHA, ONE_MINUS_SRC_ALPHA)
        /*gl.blendFuncSeparate(SRC_ALPHA, ONE_MINUS_SRC_ALPHA, ZERO, ONE)*/

        /*gl.enable(LINE_SMOOTH)
        gl.enable(POLYGON_SMOOTH)*/
    }

    fun resize(width: Int, height: Int) {
        /*log("resize: $width. $height")*/

        // 1. viewport
        gl.viewport(0, 0, width, height)

        // 2. surface
        val width = width.toFloat()
        val height = height.toFloat()
        surface.size.assign(width, height)
        surface.matrix.assignOrtho(0f, 0f, width, height)
        surface.viewport.assign(width, height)

        // 3. listeners
        for (listener in listeners) listener.onResize()
    }

    inline fun useScissorSurface(camera: Camera, block: () -> Unit) {
        val scissorRectangle: Rectangle = camera.toScissorRectangle()
        if (useCameras) {
            val (x1, y1, x2, y2) = scissorRectangle
            enableScissor(x1, y1, x2, y2)
        }
        block()
        if (useCameras) {
            disableScissor()
        }
    }

    fun enableScissor(rectangle: Rectangle) {
        val (x1, y1, x2, y2) = rectangle
        enableScissor(x1, y1, x2, y2)
    }

    fun enableScissorLocal(localRectangle: Rectangle, camera: Camera) {
        val (x1, y1, x2, y2) = camera.toScissorRectangle(localRectangle)
        enableScissor(x1, y1, x2, y2)
    }

    fun enableScissor(x1: Float, y1: Float, x2: Float, y2: Float) {
        // flip
        val y1 = surface.viewport.height - y1
        val y2 = surface.viewport.height - y2

        // round
        val left = when (precision) {
            MEDIUM -> x1.prevIntMediumPrecision
            else -> x1.roundToInt()
        }
        val top = when (precision) {
            MEDIUM -> y1.nextIntMediumPrecision
            else -> y1.roundToInt()
        }
        val right = when (precision) {
            MEDIUM -> x2.nextIntMediumPrecision
            else -> x2.roundToInt()
        }
        val bottom = when (precision) {
            MEDIUM -> y2.prevIntMediumPrecision
            else -> y2.roundToInt()
        }

        // action
        val width = right - left
        val height = top - bottom
        gl.enable(SCISSOR_TEST)
        gl.scissor(left, bottom, width, height)
    }

    fun disableScissor() {
        gl.enable(SCISSOR_TEST)
        val width = surface.viewport.width.toInt()
        val height = surface.viewport.height.toInt()
        gl.scissor(0, 0, width, height)
        gl.disable(SCISSOR_TEST)
    }

    fun clear(color: Color = Colors.blackColor) {
        gl.clearColor(color.red, color.green, color.blue, color.alpha)
        gl.clear(COLOR_BUFFER_BIT)
    }

    fun assignLineWidth(width: Float) {
        gl.lineWidth(width)
    }

}

fun DependencyBuilder.GraphicsPlugin(plugin: Plugin<Graphics>) = install(plugin)

/*convenience*/

fun Graphics.useProgram(program: GLProgram) = gl.useProgram(program)

fun Graphics.disableProgram() = gl.useProgram(null)

fun Graphics.linkShaders(program: GLProgram, vertexSource: String, fragmentSource: String) {
    /*
    log("Graphics.linkShaders")
    log(vertexSource)
    log("---")
    log(fragmentSource)
    log("---")
    */

    val vertexShader = gl.createShader(VERTEX_SHADER) ?: error("Could not create shader object")
    gl.shaderSource(vertexShader, vertexSource)
    gl.compileShader(vertexShader)
    check(gl.getShaderiv(vertexShader, COMPILE_STATUS) == TRUE) { gl.getShaderInfoLog(vertexShader) ?: "" }

    val fragmentShader = gl.createShader(FRAGMENT_SHADER) ?: error("Could not create shader object")
    gl.shaderSource(fragmentShader, fragmentSource)
    gl.compileShader(fragmentShader)
    check(gl.getShaderiv(fragmentShader, COMPILE_STATUS) == TRUE) { gl.getShaderInfoLog(fragmentShader) ?: "" }

    gl.attachShader(program, vertexShader)
    gl.attachShader(program, fragmentShader)
    gl.linkProgram(program)
    check(gl.getProgramiv(program, LINK_STATUS) == TRUE) { gl.getProgramInfoLog(program) ?: "" }
}
