package featurea.script

import featurea.splitAndTrim

class ScriptSyntaxException(message: String) : RuntimeException(message)

interface Script {
    val docket: Docket
    suspend fun executeAction(action: String, args: Args = args()): Any? = docket.executeAction(action, args)
}

fun Shell.firstCanonicalName(script: String): String {
    val firstLine = script.splitAndTrim(";").first()
    return ScriptRightOperator(this, firstLine).key
}
