package featurea.texture

import featurea.System
import featurea.normalizedPath
import featurea.opengl.GLES20
import featurea.opengl.TRUE
import featurea.runtime.Component
import featurea.runtime.import
import featurea.texture.reader.TextureAtlas
import org.khronos.webgl.WebGLRenderingContext.Companion.RGBA
import org.khronos.webgl.WebGLRenderingContext.Companion.TEXTURE_2D
import org.khronos.webgl.WebGLRenderingContext.Companion.UNPACK_FLIP_Y_WEBGL
import org.khronos.webgl.WebGLRenderingContext.Companion.UNSIGNED_BYTE
import org.w3c.dom.Image
import kotlin.coroutines.suspendCoroutine

actual suspend fun Component.loadTexture(textureAtlas: TextureAtlas) {
    val system = import<System>()
    val webgl = import<GLES20>().glContext

    return suspendCoroutine { continuation ->
        val image = Image()
        val imageSrc = "${system.workingDir ?: "bundle"}/${textureAtlas.path}".normalizedPath
        image.src = imageSrc
        image.onload = {
            webgl.pixelStorei(UNPACK_FLIP_Y_WEBGL, TRUE) // flip texture
            webgl.texImage2D(
                target = TEXTURE_2D,
                level = 0,
                internalformat = RGBA,
                format = RGBA,
                type = UNSIGNED_BYTE,
                source = image
            )
            textureAtlas.size.width = image.width.toFloat()
            textureAtlas.size.height = image.height.toFloat()
            continuation.resumeWith(Result.success(Unit))
        }
    }
}
