package learnopengl

import featurea.math.Matrix
import featurea.math.Vector
import featurea.math.toRadians
import kotlin.math.cos
import kotlin.math.sin

const val YAW: Float = -90.0f
const val PITCH: Float = 0.0f
const val SPEED: Float = 0.05f
const val SENSITIVITY: Float = 0.2f
const val ZOOM: Float = 45.0f

enum class TestCameraMovement {
    FORWARD,
    BACKWARD,
    LEFT,
    RIGHT,
}

class TestCamera {

    var Position: Vector = Vector()
    var Front: Vector = Vector()
    var Up: Vector = Vector()
    var Right: Vector = Vector()
    var WorldUp: Vector = Vector()
    var Yaw: Float = 0f
    var Pitch: Float = 0f
    var MovementSpeed: Float = 0f
    var MouseSensitivity: Float = 0f
    var zoom: Float = 0f
    private val viewMatrix: Matrix = Matrix()

    constructor(pos: Vector = Vector(), up: Vector = Vector(0.0f, 1.0f, 0.0f), yaw: Float = YAW, pitch: Float = PITCH) {
        Front = Vector(0.0f, 0.0f, -1.0f)
        MovementSpeed = SPEED
        MouseSensitivity = SENSITIVITY
        zoom = ZOOM
        Position = pos
        WorldUp = up
        Yaw = yaw
        Pitch = pitch
        updateCameraVectors()
    }

    fun viewMatrix(): Matrix = viewMatrix.assignLookAt(Position, Position + Front, Up)

    fun ProcessKeyboard(direction: TestCameraMovement, elapsedTime: Float) {
        val velocity: Float = MovementSpeed * elapsedTime
        when (direction) {
            TestCameraMovement.FORWARD -> Position += Front * velocity
            TestCameraMovement.BACKWARD -> Position -= Front * velocity
            TestCameraMovement.LEFT -> Position -= Right * velocity
            TestCameraMovement.RIGHT -> Position += Right * velocity
        }
    }

    fun ProcessMouseMovement(xoffset: Float, yoffset: Float, constrainPitch: Boolean = true) {
        var xoffset = xoffset
        var yoffset = yoffset
        xoffset *= MouseSensitivity
        yoffset *= MouseSensitivity

        Yaw += xoffset
        Pitch += yoffset

        // make sure that when pitch is out of bounds, screen doesn't get flipped
        if (constrainPitch) {
            if (Pitch > 89.0f)
                Pitch = 89.0f
            if (Pitch < -89.0f)
                Pitch = -89.0f
        }

        // update Front, Right and Up Vectors using the updated Euler angles
        updateCameraVectors()
    }

    fun ProcessMouseScroll(yoffset: Float) {
        zoom -= yoffset
        if (zoom < 1.0f) zoom = 1.0f
        if (zoom > 45.0f) zoom = 45.0f
    }

    /*internals*/

    private fun updateCameraVectors() {
        // calculate the new Front vector
        val front = Vector()
        front.x = cos(Yaw.toRadians()) * cos(Pitch.toRadians())
        front.y = sin(Pitch.toRadians())
        front.z = sin(Yaw.toRadians()) * cos(Pitch.toRadians())
        Front = front.normalize()

        // also re-calculate the Right and Up vector
        Right.assignCross(Front, WorldUp).normalize()
        Up.assignCross(Right, Front).normalize()
    }

}
