package learnopengl

import featurea.Application
import featurea.ApplicationDelegate
import featurea.featureaDir
import featurea.runtime.Artifact
import featurea.runtime.import
import featurea.runtime.injectDependencies
import featurea.window.WindowRuntime
import learnopengl.Resources.imageShader
import learnopengl.Resources.lightShader
import learnopengl.Resources.objectShader
import learnopengl.Resources.test3dShader
import learnopengl.Resources.triangleShader

/*content*/

object Resources {
    val imageShader = "shaders/image.shader"
    val test3dShader = "shaders/test3d.shader"
    val triangleShader = "shaders/triangle.shader"
    val lightShader = "shaders/Lighting/Light.shader"
    val objectShader = "shaders/Lighting/Object.shader"
}

val requiredResources: Iterable<String> = listOf(triangleShader, imageShader, test3dShader, lightShader, objectShader)

/*dependencies*/

val artifact = Artifact("learnopengl") {
    includeContentRootWithDefaultConfig { "$featureaDir/projects/whoplee/learnopengl/res" }

    include(featurea.input.artifact) // required to work with graphics on android todo improve
    include(featurea.keyboard.artifact)
    include(featurea.texture.artifact)
    include(featurea.window.artifact)

    "BlendTest" to ::BlendTest
    "ColorsTest" to ::ColorsTest
    "Context" to ::Context
    "Test2" to ::CameraTest
    "Test3D" to ::CoordinateSystemsTest
}

/*runtime*/

fun bootstrap(update: Context.(elapsedTime: Float) -> Unit) = injectDependencies(artifact) {
    WindowRuntime {
        val app = import<Application>()
        val context = import<Context>()

        app.delegate = object : ApplicationDelegate {
            override fun update(elapsedTime: Float) = context.update(elapsedTime)
        }
    }
}

