package featurea.rml

import featurea.content.loaders
import featurea.runtime.Artifact
import featurea.runtime.artifact
import featurea.runtime.configPackages
import featurea.text.TextContent

val artifact = Artifact("featurea.rml") {
    include(featurea.rml.reader.artifact)
    include(featurea.text.artifact)

    "RmlApplicationBuilder" to ::RmlApplicationBuilder
    "RmlContent" to RmlContent::class

    loaders {
        "RmlLoader" to ::RmlLoader
    }

    static {
        val textContent = import<TextContent>()
        provideComponent(RmlContent(textContent, artifact.configPackages))
    }
}
