package featurea.rml

import featurea.content.ResourceTag
import featurea.content.RmlSchema

class RmlFile {

    lateinit var rmlTag: ResourceTag
        private set
    lateinit var rmlSchema: RmlSchema
        private set
    lateinit var text: String
        private set
    lateinit var filePath: String
        private set
    lateinit var packageId: String
        private set

    suspend fun init(text: String, filePath: String, initRmlSchema: suspend RmlFile.() -> RmlSchema) {
        this.text = text
        this.filePath = filePath
        this.rmlTag = RmlFileParser.parseRmlSource(text, filePath)
        this.packageId = rmlTag.attributes["package"] ?: throw RmlFileParseException(filePath)
        this.rmlSchema = initRmlSchema()
    }

}
