package learnopengl

import featurea.input.InputEvent
import featurea.input.InputEventType
import featurea.input.InputListener
import featurea.keyboard.KeyEvent
import featurea.keyboard.KeyEventSource
import featurea.keyboard.KeyEventType
import featurea.keyboard.KeyListener
import featurea.math.*
import featurea.opengl.*
import featurea.runtime.Module
import featurea.shader.ShaderSource
import learnopengl.Resources.lightShader
import learnopengl.Resources.objectShader
import learnopengl.TestCameraMovement.*
import kotlin.math.cos
import kotlin.math.sin

fun ColorsTest(module: Module): Test = object : Test(module), InputListener, KeyListener {

    private val projection = Matrix()
    private val camera: TestCamera = TestCamera(pos = Vector(0f, 0f, 3f))
    private val camPos: Vector get() = camera.Position

    // private val camFront: Vector get() = camera.Front
    private val mouse: Point = Point(-1f, -1f)
    private var totalTime: Float = 0f
    private val totalTimeInSeconds: Float get() = totalTime / 1_000f

    // private val cubeModel = Matrix()
    private lateinit var cube: GLArrayBuffer
    private lateinit var cubeShaderProgram: GLProgram
    private lateinit var cubeShaderSource: ShaderSource

    private val lightModel = Matrix()
    private val lightPos: Vector = Vector(1.2f, 1.0f, 2.0f)

    // private val lightColor: Vector = Vector()
    private lateinit var light: GLArrayBuffer
    private lateinit var lightShaderProgram: GLProgram
    private lateinit var lightShaderSource: ShaderSource

    init {
        input.addListener(this)
        keyboard.addListener(this)
    }

    override suspend fun load() {
        // cube
        cube = gl.createBuffer() ?: return
        gl.bindBuffer(ARRAY_BUFFER, cube)
        gl.bufferData(ARRAY_BUFFER, cubeVerticesWithNormalsAndUvs, STATIC_DRAW)
        val (cubeShaderProgram, cubeShaderSource) = loadShaderOrNull(objectShader) ?: return
        this.cubeShaderProgram = cubeShaderProgram
        this.cubeShaderSource = cubeShaderSource

        // light
        light = gl.createBuffer() ?: return
        gl.bindBuffer(ARRAY_BUFFER, light)
        gl.bufferData(ARRAY_BUFFER, cubeVertices, STATIC_DRAW)
        val (lightShaderProgram, lightShaderSource) = loadShaderOrNull(lightShader) ?: return
        this.lightShaderProgram = lightShaderProgram
        this.lightShaderSource = lightShaderSource
    }

    override fun update(elapsedTime: Float) {
        super.update(elapsedTime)
        totalTime += elapsedTime

        // move the light source around the scene over time using sin
        lightPos.x = 1.0f + sin(totalTimeInSeconds) * 2.0f
        lightPos.y = sin(totalTimeInSeconds / 2.0f) * 1.0f

        // background
        gl.enable(DEPTH_TEST)
        gl.clearColor(0.1f, 0.1f, 0.1f, 1.0f)
        gl.clear(COLOR_BUFFER_BIT or DEPTH_BUFFER_BIT)

        projection.assignPerspective(near, far, camera.zoom, graphics.surface.size.aspectRatio)
        lightModel.assignTranslation(lightPos).scale(0.2f)

        /*
        lightColor.assign(sin(totalTimeInSeconds * 2f), sin(totalTimeInSeconds * 0.7f), sin(totalTimeInSeconds * 1.3f))
        val diffuse = lightColor * 0.5f
        val ambient = diffuse * 0.2f
        */

        // cube
        gl.useProgram(cubeShaderProgram)
        gl.uniformMatrix4fv(gl.getUniformLocation(cubeShaderProgram, "projection"), false, projection)
        gl.uniformMatrix4fv(gl.getUniformLocation(cubeShaderProgram, "view"), false, camera.viewMatrix())
        // gl.uniformMatrix4fv(gl.getUniformLocation(cubeShaderProgram, "model"), false, cubeModel)
        gl.uniform3f(gl.getUniformLocation(cubeShaderProgram, "cameraPosition"), camPos.x, camPos.y, camPos.z)
        diffuseTexture?.also { loadTexture(cubeShaderProgram, 0, "material.diffuse", it) }
        specularTexture?.also { loadTexture(cubeShaderProgram, 1, "material.specular", it) }
        gl.uniform1f(gl.getUniformLocation(cubeShaderProgram, "material.shininess"), 32.0f)

        // directional light
        gl.uniform3f(gl.getUniformLocation(cubeShaderProgram, "dirLight.direction"), -0.2f, -1.0f, -0.3f)
        gl.uniform3f(gl.getUniformLocation(cubeShaderProgram, "dirLight.ambient"), 0.05f, 0.05f, 0.05f)
        gl.uniform3f(gl.getUniformLocation(cubeShaderProgram, "dirLight.diffuse"), 0.4f, 0.4f, 0.4f)
        gl.uniform3f(gl.getUniformLocation(cubeShaderProgram, "dirLight.specular"), 0.5f, 0.5f, 0.5f)
        // point light 1
        gl.setUniform(cubeShaderProgram, "pointLights[0].position", lightPositions[0])
        gl.setUniform(cubeShaderProgram, "pointLights[0].ambient", 0.05f, 0.05f, 0.05f)
        gl.setUniform(cubeShaderProgram, "pointLights[0].diffuse", 0.8f, 0.8f, 0.8f)
        gl.setUniform(cubeShaderProgram, "pointLights[0].specular", 1.0f, 1.0f, 1.0f)
        gl.setUniform(cubeShaderProgram, "pointLights[0].constant", 1.0f)
        gl.setUniform(cubeShaderProgram, "pointLights[0].linear", 0.09f)
        gl.setUniform(cubeShaderProgram, "pointLights[0].quadratic", 0.032f)
        // point light 2
        gl.setUniform(cubeShaderProgram, "pointLights[1].position", lightPositions[1])
        gl.setUniform(cubeShaderProgram, "pointLights[1].ambient", 0.05f, 0.05f, 0.05f)
        gl.setUniform(cubeShaderProgram, "pointLights[1].diffuse", 0.8f, 0.8f, 0.8f)
        gl.setUniform(cubeShaderProgram, "pointLights[1].specular", 1.0f, 1.0f, 1.0f)
        gl.setUniform(cubeShaderProgram, "pointLights[1].constant", 1.0f)
        gl.setUniform(cubeShaderProgram, "pointLights[1].linear", 0.09f)
        gl.setUniform(cubeShaderProgram, "pointLights[1].quadratic", 0.032f)
        // point light 3
        gl.setUniform(cubeShaderProgram, "pointLights[2].position", lightPositions[2])
        gl.setUniform(cubeShaderProgram, "pointLights[2].ambient", 0.05f, 0.05f, 0.05f)
        gl.setUniform(cubeShaderProgram, "pointLights[2].diffuse", 0.8f, 0.8f, 0.8f)
        gl.setUniform(cubeShaderProgram, "pointLights[2].specular", 1.0f, 1.0f, 1.0f)
        gl.setUniform(cubeShaderProgram, "pointLights[2].constant", 1.0f)
        gl.setUniform(cubeShaderProgram, "pointLights[2].linear", 0.09f)
        gl.setUniform(cubeShaderProgram, "pointLights[2].quadratic", 0.032f)
        // point light 4
        gl.setUniform(cubeShaderProgram, "pointLights[3].position", lightPositions[3])
        gl.setUniform(cubeShaderProgram, "pointLights[3].ambient", 0.05f, 0.05f, 0.05f)
        gl.setUniform(cubeShaderProgram, "pointLights[3].diffuse", 0.8f, 0.8f, 0.8f)
        gl.setUniform(cubeShaderProgram, "pointLights[3].specular", 1.0f, 1.0f, 1.0f)
        gl.setUniform(cubeShaderProgram, "pointLights[3].constant", 1.0f)
        gl.setUniform(cubeShaderProgram, "pointLights[3].linear", 0.09f)
        gl.setUniform(cubeShaderProgram, "pointLights[3].quadratic", 0.032f)
        // spotLight
        gl.setUniform(cubeShaderProgram, "spotLight.position", camera.Position)
        gl.setUniform(cubeShaderProgram, "spotLight.direction", camera.Front)
        gl.setUniform(cubeShaderProgram, "spotLight.ambient", 0.0f, 0.0f, 0.0f)
        gl.setUniform(cubeShaderProgram, "spotLight.diffuse", 1.0f, 1.0f, 1.0f)
        gl.setUniform(cubeShaderProgram, "spotLight.specular", 1.0f, 1.0f, 1.0f)
        gl.setUniform(cubeShaderProgram, "spotLight.constant", 1.0f)
        gl.setUniform(cubeShaderProgram, "spotLight.linear", 0.09f)
        gl.setUniform(cubeShaderProgram, "spotLight.quadratic", 0.032f)
        gl.setUniform(cubeShaderProgram, "spotLight.cutOff", cos(12.5f.toRadians()))
        gl.setUniform(cubeShaderProgram, "spotLight.outerCutOff", cos(17.5f.toRadians()))

        gl.bindBuffer(ARRAY_BUFFER, cube)
        gl.bindAttributes(cubeShaderSource)

        for ((index, cubePosition) in cubePositions.withIndex()) {
            gl.setUniform(cubeShaderProgram, "model", matrix {
                translate(cubePosition)
                rotate(1.0f, 0.3f, 0.5f, Angle(20.0f * index))
            })
            gl.drawArrays(TRIANGLES, 0, 36)
        }


        // light
        gl.useProgram(lightShaderProgram)
        gl.setUniform(lightShaderProgram, "projection", projection)
        gl.setUniform(lightShaderProgram, "view", camera.viewMatrix())

        // >>
        /*
        gl.uniformMatrix4fv(gl.getUniformLocation(lightShaderProgram, "model"), false, lightModel)
        gl.bindBuffer(ARRAY_BUFFER, light)
        gl.bindAttributes(lightShaderSource)
        gl.drawArrays(TRIANGLES, 0, 36)
        */
        for (lightPosition in lightPositions) {
            gl.setUniform(lightShaderProgram, "model", matrix { translate(lightPosition); scale(0.2f) })
            gl.bindBuffer(ARRAY_BUFFER, light)
            gl.bindAttributes(lightShaderSource)
            gl.drawArrays(TRIANGLES, 0, 36)
        }
        // <<

    }

    override fun onInput(inputEvent: InputEvent) {
        val x = inputEvent.x2
        val y = inputEvent.y2
        if (inputEvent.type == InputEventType.DRAG) {
            camera.ProcessMouseMovement(x - mouse.x, mouse.y - y)
        }
        mouse.assign(x, y)
    }

    override fun onKey(keyEvent: KeyEvent) {
        if (keyEvent.type == KeyEventType.PRESS) {
            when (keyEvent.source) {
                KeyEventSource.W -> camera.ProcessKeyboard(FORWARD, app.elapsedTime)
                KeyEventSource.S -> camera.ProcessKeyboard(BACKWARD, app.elapsedTime)
                KeyEventSource.A -> camera.ProcessKeyboard(LEFT, app.elapsedTime)
                KeyEventSource.D -> camera.ProcessKeyboard(RIGHT, app.elapsedTime)
            }
        }
    }

}

fun testColors(module: Module): Test = ColorsTest(module).apply {
    diffuseTexture = "images/container.png"
    specularTexture = "images/containerSpecular.png"
    // emissionTexture = "images/containerEmission.jpg"
}
