package featurea.js

import featurea.runtime.RuntimeBuilder
import featurea.runtime.launch
import kotlinx.browser.document
import kotlinx.browser.window

fun loadRootElementAsBody(mainCall: () -> RuntimeBuilder) {
    window.loadBody {
        launch {
            initModule { module ->
                module.components.inject("_rootElement", window.document.body!!)
            }
            mainCall()
        }
    }
}

fun loadRootElementAsDialog(mainCall: () -> RuntimeBuilder) {
    window.loadBody {
        appendMainWindow(document.title, splashImagePath = null, 1f, mainCall)
    }
}
