package featurea.text.reader

import featurea.System
import featurea.content.Resource
import featurea.content.ResourceReader
import featurea.content.textExtensions
import featurea.hasExtension
import featurea.runtime.Container

const val TEXT_CONTENT_TYPE = "text"

class TextReader(container: Container) : ResourceReader(container, TEXT_CONTENT_TYPE) {

    private val system = container.import<System>()

    override suspend fun readResourceOrNull(resourcePath: String, isBundle: Boolean): Resource? {
        if (resourcePath.hasExtension(system.textExtensions)) {
            return Resource(resourcePath)
        }
        return null
    }

}

expect suspend fun System.readTextFileOrNull(filePath: String, limit: Int = -1): String?
