package featurea.content

import featurea.runtime.Component
import featurea.runtime.Module
import featurea.runtime.import

class ResourceLoaderNotFoundException(contentType: String) : RuntimeException(contentType)

abstract class ResourceLoader(override val module: Module, contentType: String) : Component {

    init {
        val registry = import<ResourceLoaderRegistry>()
        registry.registerResourceLoader(contentType, this)
    }

    abstract fun findResourcesOrNull(resourceTag: ResourceTag, key: String, value: String): List<String>?

    abstract suspend fun loadingResource(resource: Resource)

    abstract suspend fun releaseResource(resource: Resource)

}
