package featurea.shader

import featurea.Block
import featurea.graphics.Camera
import featurea.graphics.Graphics
import featurea.math.Point
import featurea.math.Rectangle
import featurea.opengl.GLES20
import featurea.opengl.TEXTURE_2D
import featurea.runtime.Constructor
import featurea.runtime.Module
import featurea.runtime.Component
import featurea.runtime.import

abstract class Layer(val shader: Shader) : Component {

    override val module: Module = shader.module
    private val gl = import<GLES20>()
    private val graphics = import<Graphics>()
    val uniforms: Uniforms = Uniforms(shader)
    private var builder: Block? = null
    private var isDirty: Boolean = true
    var isVisible: Boolean = true

    abstract fun render(camera: Camera)

    fun build(builder: Block = {}) {
        this.builder = builder
        builder()
    }

    /**
     * @return true if invalid
     */
    // quickfix todo avoid `useBuilder`
    fun invalidate(useBuilder: Boolean = true, block: Block): Boolean {
        val isInvalid = isDirty
        if (isInvalid) {
            if (useBuilder) builder?.invoke()
            block()
            isDirty = false
        }
        return isInvalid
    }

    open fun invalidate() {
        isDirty = true
    }

    fun update(block: Block) {
        gl.useProgram(shader.id)
        block()
        gl.bindTexture(TEXTURE_2D, null) // just for try todo delete this
        gl.useProgram(null)
    }

    fun update(scissor: Rectangle, camera: Camera, useScissor: Boolean = true, block: Block) {
        gl.useProgram(shader.id)
        if (useScissor) graphics.enableScissorLocal(scissor, camera)
        block()
        if (useScissor) graphics.disableScissor()
        gl.bindTexture(TEXTURE_2D, null) // just for try todo delete this
        gl.useProgram(null)
    }

}

@Constructor
fun Layer.VertexBuffer(applied: Int, capacity: Int = 0): VertexBuffer =
    VertexBuffer(import(), shader.attributes, capacity, applied)

fun Layer.mv(point: Point): Point = uniforms.mv(point)

fun Layer.mv(x: Float, y: Float): Point = uniforms.mv(x, y)

fun Layer.mvp(point: Point): Point = uniforms.mvp(point)

fun Layer.mvp(x: Float, y: Float): Point = uniforms.mvp(x, y)
