package featurea.window

import featurea.EventListener
import featurea.layout.Layout
import featurea.layout.Orientation

typealias WindowTask = WindowListener.() -> Unit

interface WindowListener : EventListener {
    fun onResize() {}
    fun onInvalidateLayout(layout: Layout) {}
    fun onInvalidateGraphics() {}
    fun onChangeOrientation(orientation: Orientation) {}
}

fun InvalidateGraphicsListener(task: WindowTask): WindowListener = object : WindowListener {
    override fun onInvalidateGraphics() = task()
}
