package featurea.shader.reader

import featurea.System
import featurea.content.Resource
import featurea.content.*
import featurea.extension
import featurea.replaceSuffix
import featurea.runtime.Container

const val SHADER_CONTENT_TYPE = "shader"

class ShaderReader(container: Container) : ResourceReader(container, SHADER_CONTENT_TYPE) {

    private val system = container.import<System>()

    override suspend fun readResourceOrNull(resourcePath: String, isBundle: Boolean): Resource? {
        if (resourcePath.extension == SHADER) {
            return Resource(
                if (isBundle) {
                    val cachedResource = resourcePath.replaceSuffix(".shader", "Cache.shader")
                    val absolutePath = system.findAbsolutePathOrNull(cachedResource)
                    if (absolutePath != null) cachedResource else resourcePath
                } else {
                    resourcePath
                }
            )
        }
        return null
    }

}
